.page-footer {
    padding: g(12) 0 g(11);

}
.page-footer__logo {
    img {
        margin-bottom: g(10);
        width: rem(158);
    }
}
.page-footer__logo-cont {
    display: inline-block;
    align-items: center;
}
.page-footer__top {
    margin-bottom: g(8);
}
.page-footer__menu-item {
    display: block;
    color: #1C1C1C;
    font-family: $SFProDisplay;
    font-size: 15px;
    cursor: pointer;
    padding-bottom: g(7);
    @include link-h;
}

.page-footer__title {
    color: #1F1F1F;
    font-family: $SFProDisplay;
    font-weight: 500;
    font-size: rem(17px);
    margin-bottom: g(8);
}

.page-footer__menu {
    margin: 0 auto;

}
.page-footer__menu-col:first-child {
    .page-footer__menu-item {
        @include media-breakpoint-up(lg) {
            font-weight: 500;
        }
    }

}
.page-footer__address {
    color: #1C1C1C;
    font-family: $SFProDisplay;
    font-size: rem(15px);
}

.page-footer__contact-bottom {

}

.page-footer__contact-group {
    &:not(:last-child) {
        margin-bottom: g(10);
    }
}

.page-footer__about-title {
    display: inline-block;
    color: #1C1C1C;
    font-family: $SFProDisplay;
    font-size: rem(15px);
    padding-bottom: rem(10px);
}

.page-footer__law {
    font-family: $SFProDisplay;
    font-size: 12px;
    a {
        color: #303030;
        @include link-h;
    }
}


.page-footer__menu {
    max-width: 480px;
}

.page-footer__about {
    margin-bottom: g(10);
    display: flex;
    justify-content: space-between;
}


.page-footer__about-logo {
    display: inline-block;
    img {
        width: rem(161);
    }
}

.page-footer__social-icon {
    padding: 6px;
    display: flex;
    align-items: center;
    justify-content: center;
    svg {
        height: 23px;
    }
    fill:#C4AB54;

    &:hover {
        fill:#1C1C1C;
    }
}
.page-footer__social-icon {
    display: flex;
    align-items: center;
    padding: 6px;
    cursor: pointer;
    color: #1C1C1C;
    fill:#C4AB54;
    &:hover {
        fill:#C4AB54;

    }
    svg {
        width: 21px;
        height: 21px;
        border-radius: 5px;
    }
    @include link-h;
}
.page-footer__social:first-child {
    padding-right: rem(10);
}

.page-footer__site {
    img {
        width: rem(69);
    }
}

.page-footer__social {
   display: flex;
    margin: -6px;
}

.page-footer__about-right {
    display: flex;
    flex-direction: column;
}
.page-footer__bottom-row {
    align-items: flex-end;
}
.page-footer__dev-link-ico {
    display: flex;
}
.page-footer__dev-link {
    margin-right: rem(20);
    color: #1F1F1F;
    display: flex;
    align-items: flex-end;
    line-height: 1;
    .icon__design {
        width: 83px;
        height: 25px;
    }
    .icon__aero {
        width: 83px;
        height: 12px;
    }
}
.page-footer__dev-link-text {
    margin-right: g(3);
    font-size: 14px;
}
.page-footer__dev-link:last-child {
    margin-right: 0;
}

.page-footer__copyright {
    font-size: 14px;
    color: #1F1F1F;
}
.page-footer__tel {
    margin-bottom: g(3);
}
.page-footer__tel-link {
    font-size: 15px;
    color: #1C1C1C;
    @include link-h;
    margin-bottom: g(8);
}
.page-footer__email {
    font-size: 15px;
    color: #1C1C1C;
    @include link-h;
    margin-bottom: g(8);
    display: block;
}
.page-footer__about-left {
    margin-left: g(8);
}
.page-footer__policy-link{
    display: inline-block;
    color: #1C1C1C;
    font-family: $SFProDisplay;
    font-size: rem(15px);
    &:hover{
        color: #C4AB54;
    }
}
@include media-breakpoint-only(lg) {
    .page-footer__logo-cont {
        display: none;
    }
}
@include media-breakpoint-down(lg) {
    .page-footer__logo {
        img {
            margin-bottom: g(5);
        }
    }
    .page-footer__logo-cont {
        margin-bottom: g(10);
    }
}
@include media-breakpoint-only(md) {

}
@include media-breakpoint-down(md) {
    .page-footer__about-row {
        margin-top: g(10);
    }
    .page-footer__top {
        //text-align: center;
    }
    .page-footer__social {
        margin-bottom: 0;
        //justify-content: center;
        font-size: rem(20);
    }
    .page-footer__title {
        margin-bottom: g(8);
        font-size: rem(22);
    }
    .page-footer__tel {
        margin-bottom: g(8);
        font-size: rem(22);
    }
    .page-footer__email {
        margin-bottom: g(9);
        font-size: rem(22);
    }
    .page-footer__address {
        font-size: rem(22);
    }
    .page-footer__contact {
        margin-bottom: 0;
    }
    .page-footer__about {
        margin-bottom: g(10);
    }

    .page-footer__menu-item {
        font-size: rem(22);
    }

    .page-footer__menu {
        width: 100%;
    }
    .page-footer__law {
        font-size: 12px;
    }
    .page-footer__about-title {
        font-size: 12px;
    }
    .page-footer__policy-link{
        font-size: 12px;
    }
    .page-footer__bottom {
        flex-direction: column;
        //text-align: center;
        justify-content: center;
        //align-items: center;
    }
    .page-footer__menu {
        margin-bottom: g(10);
    }
    .page-footer__dev-link-ico {
        svg {
            zoom: 0.8;
        }
    }
    .page-footer__dev-link-text {
        font-size: 12px;
        line-height: 1;
    }
    .page-footer__copyright {
        font-size: 12px;
    }
    .page-footer__menu {
        max-width: none;
    }
}
@include media-breakpoint-down(sm) {
    .page-footer__develop {

    }
    .page-footer__bottom-row {
        //flex-direction: column-reverse;
    }
    .page-footer__dev-link {
        font-size: 12px;
        margin-bottom: 10px;
    }
    .page-footer__dev-link-text {
        //width: 50%;
        //text-align: right;
    }
}
@include media-breakpoint-down(xs) {
    .page-footer__title {
        margin-bottom: g(7);
    }
    .page-footer__address {
        font-size: 14px;
    }
    .page-footer__menu-row {
        @include gutter(5);
    }
    .page-footer__menu-item {
        font-size: 13px;
    }
    .page-footer__about-logo {
        text-align: left;
    }
    .page-footer__copyright {
        font-size: 12px;
    }


    .page-footer__logo img {
        margin-bottom: g(5);
    }

}
