.page-header {
    position: relative;
    background: #fff;
    box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.08);

    &.fixed-header {
        background: #fff;
        box-shadow: 0 0 3px 0px #717171;

        @include media-breakpoint-down(md) {
            display: none !important;
        }
        @media (max-height: 730px) {
            display: none !important;
        }
        .page-header__top {
            display: none !important;
        }
    }
}
.page-header__burger {
    text-align: center;
    display: inline-block;
    position: relative;
    z-index: 180;
}
.page-header__burger-mobile {
    opacity: 0;
}
.page-header__top {
    background: #114034;
    text-align: center;
    align-items: center;
    padding-top: g(5);
    padding-bottom: g(5);
    color: #fff;
    font-family: $SFProDisplay;
    font-weight: 500;
    font-size: rem(13px);
    display: block;

}
.page-header__content {
    padding: g(7) 0;
}
.page-header__row {
    display: flex;
    align-items: center;
}

.page-header__menu {
    border-right: 1px solid #8A8A8A;
    margin-right: rem(27);
    padding-right: g(5);
}


.page-header__menu-row {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    flex-shrink: 0;
}

.page-header__logo {
    padding-right: rem(40);
    line-height: 1;
    width: rem(140px);
    flex-shrink: 0;
}

.page-header__menu-link {
    font-family: $SFProDisplay;
    font-weight: 600;
    font-size: 12px;
    color: #343434;
    padding: g(8) g(4);
    text-transform: uppercase;
    white-space: nowrap;
    @include link-h;
}

.page-header__left-row {
    display: flex;
    flex-direction: row;
    align-items: center;
    padding-right: 10px;
    flex-shrink: 0;
}
.page-header__right-row {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-left: auto;
    flex-shrink: 0;
}

.page-header__more-text {
    font-family: $SFProDisplay;
    font-weight: 600;
    font-size: 11px;
    color: #114034;
    text-transform: uppercase;
}

.page-header__tel {
    padding-right: rem(23px);
    font-family: $SFProDisplay;
    font-weight: 500;
    font-size: rem(16px);
    line-height: 135.34%;
    color: #1C1C1C;
    white-space: nowrap;
    @include link-h;
}

.page-header__site {
    padding-right: rem(23px);
    flex-shrink: 0;
    img {
        width: rem(40px);
        @include media-breakpoint-up(xl) {
            width: rem(56);
        }
    }
}

.page-header__fav {
    cursor: pointer;
    padding-right: rem(18px);
    padding-left: rem(025);
    border-left: 1px solid #8A8A8A;
    transition: 0.2s;
    fill: #121212;

    &:hover {
        fill: #C4AB54;
    }
}

.page-header__call {
    width: rem(205px);
}
.page-header__call-btn {
    @include btn;
    @include btn-block;
    font-weight: 500;
    font-size: 15px;
    &:hover {
        background: #C4AB54;
        color: #fff;
    }
}
.page-header__mobile-menu {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    @include min-height-in-vh(100);
    overflow-y: auto;
    overflow-x: hidden;
    background: #fff;
    padding: g(10) 0;
    display: none;
    z-index: 170;

}

.page-header__link {
    padding: g(5) 0;
    display: block;
}

.page-header__items {
    align-items: center;
    text-align: left;

}

.page-header__burger-text {
    display: none;
    color: #114034;
    font-family: $SFProDisplay;
    font-weight: 500;
    font-size: 9px;
}

.page-header__burger-link {
    font-family: $SFProDisplay;
    font-weight: 600;
    font-size: rem(24px);
    color: #343434;
    padding-bottom: rem(20px);
}

.page-header__burger-menu {
    display: flex;
    flex-direction: column;
    padding-top: rem(70px);
}

.page-header__burger-about-menu {
    display: flex;
    flex-direction: column;
    padding-top: rem(70px);
}

.page-header__burger-about-link {
    font-family: $SFProDisplay;
    font-weight: normal;
    font-size: rem(24px);
    color: #343434;
    padding-bottom: rem(20px);
}

.page-header__burger-logo {
    img {
        margin-bottom: g(12);
    }
}

.page-header__dropdown-menu {
    position: absolute;
    left: 0;
    top: 100%;
    right: 0;
    background: #F7F7F7;
    padding: rem(40px) 0 rem(200) 0;
    opacity: 0;
    pointer-events: none;
    transition: 0.2s;
    z-index: 10;

    &_open {
        opacity: 1;
        pointer-events: all;
    }
}

.page-header__dropdown-link {
    padding-bottom: rem(11);
    color: #1C1C1C;
    font-family: $SFProDisplay;
    font-size: rem(15px);
    font-weight: normal;
    @include link-h;
}

.page-header__dropdown-col {
    display: flex;
    flex-direction: column;
}

.page-header__dropdown-link {
    color: #1C1C1C;
    font-family: $SFProDisplay;
    font-size: rem(15px);
    font-weight: normal;
}
@include media-breakpoint-only(lg) {
    .page-header__call-btn {
        font-size: 12px;
    }
}
@include media-breakpoint-down(md) {
    .page-header__logo {
        padding-right: rem(30);
        max-width: rem(160);
        z-index: 180;
    }
    .page-header__top {
        display: none;
    }
    .page-header__menu-row {
        display: none;
    }
    .page-header__call {
        display: inline-block;
        z-index: 180;
    }

    .page-header__menu-link {
        font-family: $SFProDisplay;
        font-weight: 600;
        font-size: rem(27px);
        color: #343434;
        padding-bottom: g(12);
        display: flex;
        flex-direction: column;
    }
    .page-header__burger-text {
        display: inline-block;
    }

    .page-header__site {
        padding-right: rem(20);
    }
    .page-header__menu {
        margin-right: rem(10);
    }
}
@include media-breakpoint-down(lg) {
    .page-header__menu {
        margin-right: rem(10);
    }
}


.page-header__icon-more-open {
    display: none;
}

.page-header__more {
    text-align: center;
}

.page-header__more-img {
    svg {
        width: 20px;
        height: 20px;
    }

    &_open {
        .page-header__icon-more {
            display: none;
        }

        .page-header__icon-more-open {
            display: block;
        }
    }
}
.page-header__burger {
    display: none;
}
@include media-breakpoint-only(lg) {
    .page-header__logo {
        width: 110px;
        padding-right: 10px;
    }
}
@include media-breakpoint-down(md) {
    .page-header__burger {
        display: block;
        max-width: rem(30px);
        margin-right: rem(30px);
    }
}
@include media-breakpoint-down(sm) {

    .page-header__tel {
        display: none;
    }
    .page-header__fav {
        border-left: 0;
    }
    .page-header__more {
        display: none;
    }
    .page-header__menu-row {
        display: none;
    }

    .page-header__site {
        display: none;
    }
    .page-header__logo {
        max-width: 150px;
        padding-right: 5px;
    }
    .page-header__call-btn {
        font-size: 12px;
    }
    .page-header__fav {
        padding: 5px;
    }
    .page-header__call {
        width: 180px;
    }
}

.page-header__mobile-menu {
    background: #EFEFEF;
}
@include media-breakpoint-down(sm) {
    .page-header__logo {
       img {
           max-height: 41px;
       }
    }
}

