.b-mort-apart{
    padding: g(12) 0;
}
.b-mort-apart-item {
    border: 1px solid #C0AB61;
    padding: g(10);
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.b-mort-apart-item__desc {
    margin-bottom: g(14);
}
.b-mort-apart-item__title {
    font-family: $Montserrat;
    font-weight: 500;
    font-size: rem(41px);
    color: #203E34;
    margin-bottom: g(5);
}

.b-mort-apart-item__add {
    font-weight: normal;
    font-size: rem(21px);
    color: #2D483F;
}
.b-mort-apart-item__price-text{
    font-weight: normal;
    font-size: rem(27px);
    color: #2D483F;
    margin-bottom: g(9);
}
.b-mort-apart-item__price-btn{
    @include btn;
    @include btn-yellow;
}
@include media-breakpoint-down(xl) {
    .b-mort-apart-item__title {
        font-size: rem(32px);
    }
}
