.b-documents {
    margin-bottom: g(12);
}
.b-documents__content {
    flex-grow: 1;
    background-size: cover;
    background-position: center;
    padding: g(12) 0;
}
.b-documents__row {
    align-items: center;
}
.b-documents_col-sale {
    display: flex;
    justify-content: center;
}
.b-documents-about__img {
    img {
        width: 100%;
    }
}
@include media-breakpoint-down(md) {
    .b-documents__row {
        align-items: start;
    }
}

