.b-dev-slider {
    text-align: center;
    padding-bottom: g(13);
    .swiper-button-prev,
    .swiper-button-next {
        position: absolute;
        top: 50%;
        margin-top: rem(-18px);
        z-index: 10;
        cursor: pointer;
        background: none;
        align-items: center;
        display: flex;
        justify-content: center;
    }

    .swiper-button-prev {
        transform: rotate(180deg);
    }

}
.b-dev-slider__slide {
    padding-bottom: 40%;
    @include cover;
}
.b-dev-slider__title {
    color: #1F1F1F;
    font-family: $Montserrat;
    font-weight: 500;
    font-size: rem(24px);
    margin-bottom: g(11);
}
@include media-breakpoint-down(md) {
    .b-dev-slider__slide {
        padding-bottom: 70%;
    }
    .b-dev-slider {
        padding-bottom: g(12);
    }
    .b-dev-slider__title {
        margin-bottom: g(10);
    }
}
