.b-3d-tour {

}
.b-3d-tour__title{
    color: #114034;
    font-family: $Montserrat;
    font-weight: 500;
    font-size: rem(24);
    margin-bottom: g(8);
}
.b-3d-tour__content {
    position: relative;
}
.b-3d-tour__frame {
    position: relative;
    padding-bottom: 49.5%;
    background: #eee;

    iframe {
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        z-index: 5;
    }
}
.b-3d-tour__link {
    @include btn;
    @include btn-yellow;
    position: absolute;
    z-index: 10;
    bottom: g(8);
    left: g(8);
}

@include media-breakpoint-down(sm) {
    .b-3d-tour__frame {
        padding-bottom: 0;
        @include min-height-in-vh(70);
    }
    .b-3d-tour__link {
        position: static;
        margin-top: g(10);
    }
}
