
.b-video {
    height: 100%;
}

.b-video__video {
    position: relative;
    height: 100%;
    //
    iframe {
        //max-height: rem(493);
        display: block;
    }
}

.modal-content {
    .b-video__video {
        height: rem(580);
    }
}

.b-video__image {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-size: cover;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid #D7D7D7;
    box-sizing: border-box;
    border-radius: 3px;
    &:after {
        content: '';
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        background-size: cover;
        background-position: center;
        background: rgba(0, 0, 0, 0.32);
    }
}


.b-video__video-btn {
    position: relative;
    flex-shrink: 0;
    cursor: pointer;
    transition: 0.2s;
    z-index: 15;

    background: rgba(#FFFFFF, 0.89);
    box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.08);
    display: flex;
    align-items: center;
    justify-content: center;
    width: rem(57);
    height: rem(57);
    border-radius: 50%;
    svg {
        width: 38%;
        height: 38%;
    }

    &:hover {
        transform: scale(1.1);
    }

    &:active {
        transform: scale(0.9);
    }
}





@include media-breakpoint-only(md) {
    .b-video__video-btn {
        //transform: translateY(90%);
    }
    .modal .b-video__video {
        margin: 20px -20px 0;
    }
}


@include media-breakpoint-down(sm) {
    .b-video__video-btn {
        width: 45px;
        height: 45px;
    }
    .modal .b-video__video {
        margin: 20px -15px 0;
    }
    .b-video__info {
        padding-left: 50px;
        left: 30px;
        border-radius: 5px;
    }
}

@include media-breakpoint-down(xs) {
    .b-video__image {
        //justify-content: flex-start;
        padding: 15px;
    }
}
