.b-about-apartments {

    .b-nav__link {
        color: #000;
        cursor: pointer;
        &:hover {
            color: #C4AB54;
        }
    }
    .b-nav__name {
        display: none;
    }
}
.b-about-apartments__title {
    color: #1E1E1E;
    font-family: $Montserrat;
    font-weight: 500;
    font-size: rem(24);
    margin-bottom: g(6);
    display: flex;
    align-items: center;
}
.b-about-apartments__sale {
    color: #FFFFFF;
    font-weight: 600;
    font-size: rem(21px);
    line-height: 1;
    background: #C4AB54;
    opacity: 0.8;
    border-radius: 3px;
    padding: rem(13) rem(15);
    display: inline-block;
    margin-right: g(7);
    flex-shrink: 0;
}
@include media-breakpoint-down(md) {
    .b-about-apartments {
        margin-bottom: g(0);
    }
}
