
.b-checkbox-group {

}
.b-checkbox-group__items {
    margin: -4px;
    display: flex;
    flex-wrap: wrap;
}
.b-checkbox-group__item {
    position: relative;
    margin: 4px;
    flex-shrink: 0;
}
.b-checkbox-group__input {
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
    pointer-events: none;
    &:checked {
        &+.b-checkbox-group__Label {
            background: #C4AB54;
        }
    }
}
.b-checkbox-group__Label {
    font-size: 15px;
    min-width: 45px;
    height: 45px;
    padding: g(5);
    display: flex;
    align-items: center;
    justify-content: center;

    color: #FFFFFF;
    background: #A7A7A7;
    box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.08);
    border-radius: 3px;
    cursor: pointer;
    transition: 0.2s;
    &:hover {
        background: #9a9a9a;
    }
}
