.b-layout{
    background: #fff;
    padding-top: g(13);
    padding-bottom: g(12);
}
.b-layout__img{
   position: relative;
}
.b-layout__img-text{
    max-width: 200px;
    top: rem(34);
    left: rem(44);
    position: absolute;
    color: #FFFFFF;
    font-family: $Montserrat;
    font-weight: 500;
    font-size: rem(28px);
    span {
        color: #C4AB54;
    }
}
.b-layout__row {
    align-items: center;
}
@include media-breakpoint-down(md) {
    .b-layout {
        padding-top: g(12);
    }
    .b-layout__img{
        margin-bottom: g(7);
    }
}
