.b-main {
    position: relative;
    @include testFix;
    @include min-height-in-vh(100);
    display: flex;
    flex-direction: column;

}

.b-main__content {
    flex-grow: 1;
    background-size: cover;
    background-position: center;
    padding: g(13) 0;
    position: relative;
    &:before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: #000;
        opacity: 0.35;
    }
    &>.container {
        position: relative;
        z-index: 10;
    }


}

.b-main__title {
    z-index: 10;
    color: #C4AB54;
    font-family: $Montserrat;
    font-weight: 500;
    font-size: rem(70px);
    text-transform: lowercase;
    text-align: center;
    max-width: 869px;
    margin: 0 auto;
    margin-bottom: g(4);
}

.b-main__desc {
    color: #FFF;
    font-family: $SFProDisplay;
    font-weight: normal;
    font-size: rem(30px);
    z-index: 10;
    margin-bottom: g(8);
    text-align: center;
}

.b-main__link {
    font-family: $Montserrat;
    font-weight: 500;
    font-size: rem(17px);
    height: rem(253px);
    width: rem(253px);
    display: flex;
    flex-direction: column;
    justify-content: center;
    background-color: #114034;
    border-bottom-left-radius: 48%;
    border-top-right-radius: 48%;

}

.b-main__link-title {
    text-transform: uppercase;
    color: #FFF;
    margin-bottom: var(--g5);
}

.b-main__link-add {
    color: #C4AB54;
}

.b-main__link-button {
    font-family: $SFProDisplay;
    font-weight: 500;
    font-size: rem(15px);
    color: #fff;
    padding: rem(10) rem(17) rem(11) rem(17);
    border: rem(2px) solid #C4AB54;
    box-sizing: border-box;
    filter: drop-shadow(1px 1px 3px rgba(0, 0, 0, 0.08));
    border-radius: rem(3px);
}
.b-main__page-header {
    background: #fff;
    position: relative;
    z-index: 20;
}

@include media-breakpoint-down(md) {
    .b-main__desc {
        margin-bottom: g(13);
    }

}
@include media-breakpoint-down(xs) {
    .b-main__desc {
        margin-bottom: g(13);
    }
    .b-main__content {
        padding: g(12) 0;
    }
    .b-main__title {
        font-size: 28px;
    }
}



