
.b-distance {
    position: relative;

    .ymaps-2-1-79-ground-pane {
        filter: grayscale(1);
    }
    .ymaps-2-1-79-places-pane {
        z-index: 3000 !important;
    }
    .ymaps-2-1-79-listbox,
    .ymaps-2-1-79-traffic,
    .ymaps-2-1-79-controls__toolbar_left,
    .ymaps-2-1-79-map-copyrights-promo
    {
        display: none;
    }
    .ymaps-2-1-79-placemark-overlay:hover {
        z-index: 4000 !important;
    }
}
.b-distance__top {

}
.b-distance__map {
    min-height: 80vh;
    flex-grow: 1;
    @include testFix;
    background: #eee;
}

.b-distance__marker {
    width: rem(25);
    height: rem(25);
    border-radius: 3px;
    background-color: #C4AB54;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: rem(13);
    position: relative;
    z-index: 50;

    &_active {
        background-color: #193348;
    }

    &_open {
        z-index: 100;
    }
    &_feature {
        background: #fff;
        border: 2px solid #999;
        .b-distance__marker-ico {
            filter: none;
            opacity: 0.5;
        }
    }

    &:hover {
        z-index: 60;

        .b-distance__marker-hidden {
            display: block;
        }
    }
}

.b-distance__marker_jk {
    background: transparent;
    width: rem(60);
    height: rem(60);
    object-fit: contain;
    display: flex !important;
}

.b-distance__marker-ico {
    width: 60% !important;
    height: 60% !important;
    object-fit: contain;
    filter: invert(1) hue-rotate(288deg);
}

.b-distance__marker-hidden {
    position: absolute;
    bottom: 100%;
    right: 50%;
    margin-bottom: 10px;
    background: #fff;
    padding: 20px 16px;
    min-width: 239px;
    display: none;
    z-index: 12;
    animation: 0.2s top forwards;
    box-shadow: 0 0 3px 0px rgba(113, 113, 113, 0.21);
}

.b-distance__marker-title {
    font-size: 18px;
    line-height: normal;
    color: #2F3337;
    font-family: $Montserrat;
}

.b-distance__marker-text {
    font-weight: 300;
    font-size: 14px;
    line-height: 15px;
    color: #000000;
    font-style: italic;
    margin-top: g(8);

}

.b-distance__marker-img {
    border: rem(4) solid #D6B37E;
    border-radius: 50%;
    position: absolute;
    top: 100%;
    left: 100%;
    transform: translate(-50%, -50%);
    width: rem(77);
    height: rem(77);
    display: block;
}

