}

.b-distance__marker {

}

.b-distance__marker_jk {

}

.b-distance__marker-ico {

}

.b-distance__marker-hidden {

}

.b-distance__marker-title {

}

.b-distance__marker-text {

}

.b-distance__marker-img {

}

.b-distance__content {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    pointer-events: none;
    padding: g(12);
    z-index: 20;

}

.b-distance__container {
    display: flex;
    height: 100%;
}

.b-distance__white {
    background: rgba(255, 255, 255, 0.9);
    padding: g(10);
    max-width: 663px;
    margin-left: auto;
    margin-top: auto;
    pointer-events: all;
    box-shadow: 0 0 3px 0px rgba(113, 113, 113, 0.21);
}

.b-distance__ico {
    img {
        max-width: rem(95px);
        max-height: rem(95px);
    }

    margin-bottom: g(5);
}

.b-distance__title {
    color: #1F1F1F;
    font-weight: 500;
    font-size: rem(24px);
    font-family: $Montserrat;
    margin-bottom: g(7);
}

.b-distance__text {
    font-size: 15px;
    @include text-last-mb-fix;
    margin-bottom: g(10);
    line-height: 1.5;
    max-width: 600px;
}

.b-distance__list {
    display: flex;
    flex-wrap: wrap;
    margin: -5px;
}

.b-distance__list-title {

}

.b-distance__list-item {
    margin: 5px;
    background: #D7D7D7;
    border-radius: 3px;
    color: #303030;
    font-size: 13px;
    padding: g(3) g(5);
    display: flex;
    align-items: center;
    justify-content: center;
    fill: #303030;
    cursor: pointer;
    transition: 0.2s;

    &:hover {
        background: #c2c1c1;
    }

    &_active {
        background: #C4AB54 !important;
        color: #fff !important;
        fill: #fff !important;
    }

    svg, img {
        width: 15px;
        height: 15px;
        margin-right: g(5);
    }
}
@include media-breakpoint-down(lg) {

}
@include media-breakpoint-down(md) {
    .b-distance__content {
        position: static;
        padding-top: 0;
        padding-left: 0px;
        padding-right: 0px;
        padding-bottom: 30px;
    }
    .b-distance__container {
        @include make-container();
        @include make-container-max-widths();
    }
    .b-distance__white {
        padding: 0;
        margin-left: 0;
        box-shadow: none;
    }
}
