
.b-video-section {

}
.b-video-section__video {
    height: 30vw;
    margin-bottom: g(12);
}
.b-video-section__title {
    color: #1F1F1F;
    font-family: $Montserrat;
    font-weight: 500;
    font-size: rem(24px);
    margin-bottom: g(10);
}
@include media-breakpoint-down(md) {
    .b-video-section__video {
        height: 45vw;
        margin-bottom: g(12);
    }
}
@include media-breakpoint-down(sm) {
    .b-video-section__video {
        height: 60vw;
        margin-bottom: g(12);
    }
}
