
.b-icon {
    display: flex;
}
.b-icon__ico {
    width: rem(75);
    height: rem(75);
    background: #CBA470;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-shrink: 0;
    margin-right: rem(25);

    img {
        display: block;
        max-width: 60%;
        max-height: 80%;

    }
}
.b-icon__title {
    font-family: $SFProDisplay;
    font-size: rem(29);
    color: #58524E;
    margin-bottom: rem(12);
}
.b-icon__text {
    font-family: $SFProDisplay;
    font-weight: 300;
    font-size: rem(20);
    color: #484340;
}
.b-icon_small {
    .b-icon__title {
        font-size: rem(22);
    }
    .b-icon__ico {
        width: rem(55);
        height: rem(55);
        margin-right: rem(18);
    }
}
.b-icon_noto {
    .b-icon__title {
        font-family: $SFProDisplay;
        font-weight: 500;
        font-size: rem(24);
    }
    .b-icon__ico {
        margin-right: rem(30);
    }
}
