.gamburger {
  font-size: 10px;
  width: 25px;
  height: 20px;
  transition: 1s;
  position: relative;
  cursor: pointer;
}

@media (max-width: 767.98px) {
  .gamburger {
    font-size: 7px;
  }
}

.gamburger span {
  display: block;
  width: 100%;
  height: 3px;
  background: #114034;
  position: absolute;
}

.gamburger-line1 {
  top: 0;
  left: 0;
  transition: 0.4s;
}

.gamburger-line2 {
  top: 50%;
  margin-top: -1.5px;
  left: 0;
}

.gamburger-line3 {
  top: 100%;
  margin-top: -3px;
  left: 0;
  transition: 0.4s;
  transform-origin: right center;
}

.gamburger.open .gamburger-line1 {
  transform: rotate(45deg);
  top: 50%;
  margin-top: -1.5px;
}

.gamburger.open .gamburger-line2 {
  position: absolute;
  right: -400vw;
  left: auto;
}

.gamburger.open .gamburger-line3 {
  transform: rotate(-45deg);
  top: 50%;
  margin-top: -1.5px;
  transform-origin: center;
}
