.b-choice-apart {
    padding: g(12) 0;
    background: #F5F5F5;
    @include selectric;
}

.b-choice-apart__row {
    margin-bottom: -30px;
}

.b-choice-apart__input {
    margin-bottom: 30px;
}

.b-choice-apart__bottom {
    margin-bottom: g(10);
    align-items: end;
}

.b-choice-apart__label {
    margin-bottom: g(7);
}

.b-choice-apart__btn {
    @include btn;
    @include btn-yellow;
}

.b-choice-apart__input-text {
    color: #1F1F1F;
    font-size: 15px;
    margin-bottom: g(8);
    &_hidden {
        opacity: 0;
    }
}
.b-choice-apart__checkbox-list {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 5px;
}
@include media-breakpoint-down(md) {
    .b-choice-apart__group {
        margin-bottom: g(9);
    }
    .b-choice-apart__input {
        margin-bottom: g(12);
    }
    .b-choice-apart__btn {
        width: 100%;
        //font-size: 18px;
    }
}
@include media-breakpoint-down(sm) {
    .b-choice-apart__checkbox-list {


    }
    .b-choice-apart__checkbox {

    }
}
