.b-modal-partner {
    background: #24362f;
    .b-checkbox__label {
        color: #fff;
        font-size: 13px;
    }
}
.b-modal-partner__top {
    padding: rem(55) rem(25) 0 rem(25);
}
.b-modal-partner__title {
    font-size: rem(36);
    color: #fff;
    text-transform: uppercase;
    font-weight: normal;
}
.b-modal-partner__input {
    margin-bottom: g(9);
}

.b-modal-partner__form-control {
    padding: rem(16);
    width: 100%;
}
.b-modal-partner__title {
    margin-bottom: g(9);
}
.b-modal-partner__desc {
    color: #fff;
    font-size: 13px;
    margin-bottom: g(8);
}
.b-modal-partner__call {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: g(9);
}
.b-modal-partner__btn-text {
    color: #fff;
    font-size: 15px;
    font-weight: 500;
    p {
        margin: 0;
    }
}
.b-modal-partner__btn{
    font-size: 16px;
    color: #000;
    text-transform: uppercase;
    background: #b9a654;
    padding: rem(10) rem(20);
}
.b-modal-partner__tel {
    color: #fff;
    font-size: 15px;
    font-weight: 500;
}
.b-modal-partner__img {
    img {
        width: 100%;
        height: 100%;
    }
}