.b-promotions__row {
    align-items: center;
    @include gutter-y(30)
}

.b-promotions__content {
    position: relative;

}
.b-promotions_reverse {
    .b-promotions__row {
        flex-direction: row-reverse;
    }
}

.b-promotions__title {
    font-weight: 700;
    font-size: rem(20px);
    color: #343434;
    margin-bottom: g(10);
    @include media-breakpoint-down(md) {
        font-size: 17px;
    }
}

.b-promotions__text {
    font-weight: 400;
    font-size: 15px;
    color: #343434;
    margin-bottom: g(10);
}

.b-promotions__btn {
    @include btn;
    @include btn-yellow;
    @include btn-small;
}

.b-promotions__img {
    img {
        width: 100%;
    }
}

