.b-benefit {
    //padding-bottom: g(11);

}
.b-benefit__content {
    border: 1px solid #D7D7D7;
    border-radius: 3px;
    overflow: hidden;
}
.b-benefit__row {
    //align-items: center;
}
.b-benefit__col-text {
    padding: g(10);
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.b-benefit__title {
    color: #1C1C1C;
    font-family: $Montserrat;
    font-weight: 500;
    font-size: rem(20px);
    margin-bottom: g(8);
}


.b-benefit__img {
    height: 100%;
    img {
        height: 100%;
        width: 100%;
        object-fit: cover;
        display: block;
        //border-radius: 0px 3px 3px 0px;
    }

}

.b-benefit__text {
    ul {
        padding: 0;
        margin: 0;
    }

    li {
        position: relative;
        padding-left: rem(32);
        list-style: none;
        display: flex;
        align-items: center;
        margin-bottom: g(7);
        color: #1C1C1C;
        font-weight: 500;
        font-size: 15px;

        &:after {
            content: '';
            height: rem(16px);
            width: rem(22px);
            position: absolute;
            top: 0;
            left: 0;
            background: url("../img/general/check2.svg");
            background-size: 100% 100%;
            @include media-breakpoint-down(sm) {
                top: 0.1em;
            }
        }
    }
}

.b-benefit__item-check {
    margin-right: rem(13px);
}

.b-benefit_vertical {
    .b-benefit__img {
        //margin-bottom: g(11);
    }
}

@include media-breakpoint-down(md) {
    .b-benefit__title {
        margin-bottom: g(9);
        font-size: rem(24);
    }
    .b-benefit__img {
        margin-bottom: g(11);
    }
}

@include media-breakpoint-down(sm) {
    .b-benefit__text {
        ul {
            li {
                font-size: 13px;
                font-weight: 400;
                margin-bottom: g(5);
            }
        }

    }

}

