.b-building-inner {

    .fade-enter-active {
        transition: opacity .5s;
    }

    .fade-enter, .fade-leave-to /* .fade-leave-active до версии 2.1.8 */
    {
        opacity: 0;
    }
}

.b-building-inner__nav {
    max-width: 850px;
    margin: 0 auto;
    text-align: center;
}

.b-building-inner__nav-group_year {
    margin-bottom: g(8);
}

.b-building-inner__nav-group_months {
    margin-bottom: g(10);
}

.b-building-inner__desc {
    text-align: start;
}

.b-building-inner__desc-title {
    margin-bottom: g(9);
}

.b-building-inner__nav-item {
    cursor: pointer;
    font-weight: normal;
    font-size: 13px;
    color: #000;
    display: inline-block;
    margin: 0 var(--g1);
    padding: g(5) g(6);
    user-select: none;
}

.b-building-inner__nav-group_year {
    .b-building-inner__nav-item {
        cursor: pointer;
        font-weight: normal;
        font-size: rem(20px);
        display: inline-block;
    }

    .b-building-inner__nav-item_active {
        background: #C4AB54;
        border-radius: 3px;
        color: #fff;
    }
}

.b-building-inner__nav-group_months {
    .b-building-inner__nav-item {
        background: #D7D7D7;
        border-radius: 3px;
        color: #fff;
        margin: g(3);
        padding: g(5) g(6);
    }

    .b-building-inner__nav-item_active {
        background: #C4AB54;
        border-radius: 3px;
        color: #fff;
    }
}

.b-building-inner__link {
    @include media-breakpoint-up(lg) {
        @include btn;
        @include btn-transparent;
        @include btn-block;

        display: flex;
        align-items: center;
        justify-content: center;
    }
    display: flex;
    align-items: center;
    justify-content: center;
    svg {
        transform: rotate(180deg);
        margin-right: g(5);
    }
}



@include media-breakpoint-down(md) {
    .b-building-inner__nav-group {
        overflow: auto;
        white-space: nowrap;
        padding-bottom: 10px;
    }
}

@include media-breakpoint-down(md) {
    .b-building-inner__nav-item {
        font-size: 16px;
        padding: rem(15) rem(25);
    }
    .b-building-inner__link {
        margin-bottom: g(10);
        justify-content: flex-start;
    }
    .b-building-inner__link {
        color: #000;
    }
}

@include media-breakpoint-down(sm) {
    .b-building-inner__nav-group_year {
        margin-bottom: 0;
    }
    .b-building-inner__link {
        margin-bottom: g(12);
    }
}
