.b-cards__item {
    border: 1px solid #C0AB61;
    box-sizing: border-box;
    padding: g(10) g(10) g(6) g(10);
    @include cover;
    aspect-ratio: 1/1;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
}

.b-cards__title {
    font-weight: 600;
    font-size: rem(18px);
    line-height: 1.29;
    color: #000000;
}

.b-cards__btn {
    @include btn;
    @include btn-yellow;
    @include btn-block;
    margin-top: g(7);
}
@include media-breakpoint-down(md) {
    .b-mort-cards__row {
        @include gutter-y(30);
    }
    .b-cards__title {
        font-size: rem(22px);
    }
    .b-cards__btn {
        padding:  g(5) g(7);
        font-size: 14px;
    }
}
@include media-breakpoint-down(sm) {
    .b-cards__btn {
        padding:  g(5) g(7);
        font-size: 14px;
    }
}
