.b-excurs-item {
    background: #FFFFFF;
    border: 1px solid #B9B9B9;
    box-sizing: border-box;
    box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.08);
    border-radius: 3px;
    padding: rem(12) rem(15) rem(35) rem(15);
    height: 100%;
}
.b-excurs-item__desc {
    text-align: start;
}

.b-excurs-item__img {
    margin-bottom: g(6);
    border-radius: 3px;
    img {
        width: 100%;
    }
}

.b-excurs-item__title {
    color: #C4AB54;
    font-family: Montserrat;
    font-weight: 500;
    font-size: rem(18px);
    margin-bottom: g(6);
}

.b-excurs-item__text {
    color: #343434;
    font-weight: normal;
    font-size: 15px;
}
