}
.b-purchase__title {
    color: #1F1F1F;
    font-family: $Montserrat;
    font-weight: 500;
    font-size: rem(24px);
    text-align: center;
    padding-bottom: g(10);
    background: #fff;
}



.b-purchase__more-btn {
   @include btn;
   @include btn-transparent;
   margin-top: g(10);
}
.b-purchase__list-apart {

}
.b-purchase__row {
    margin-bottom: -30px;
}
.b-purchase__item {
