.b-order{
    background: #F9F9F9;
    border: 1px solid #E1E1E1;
    box-sizing: border-box;
    border-radius: 3px;
    padding: g(10);
    height: 100%;
}
.b-order__btn {
    @include btn;
    @include btn-yellow;
    @include btn-block;
}
.b-order__title {
    font-family: $Montserrat;
    font-weight: 500;
    font-size: 18px;
    color: #1F1F1F;
    margin-bottom: g(4);

}
.b-order__text {
    font-size: 17px;
    color: #1F1F1F;
}
@include media-breakpoint-down(md) {
    .b-order__title {
        font-size: 17px;
    }
    .b-order__text {
        margin-bottom: g(7);
        font-size: 14px;
    }
    .b-order__btn {
        font-size: rem(20px);
    }
}

