.b-build-video {
    text-align: center;

}
.b-build-video__content {
    max-width: 1100px;
    margin: 0 auto;
}
.b-build-video__row {
    margin-bottom: -30px;
}
.b-build-video__col {
    margin-bottom: 30px;
}
.b-build-video__video {
    height: rem(294px);
    margin-bottom: g(9);
}

.b-build-video__title {
    color: #1F1F1F;
    font-family: $Montserrat;
    font-weight: 500;
    font-size: rem(24px);
    margin-bottom: g(11);

}
.b-build-video__text {
    color: #C4AB54;
    font-weight: normal;
    font-size: 15px;
}
