.b-apart-plan {
    border: 1px solid #D7D7D7;
    box-sizing: border-box;
    border-radius: 3px 0px 0px 3px;
    margin-top: g(9);

    .nav-apart {
        font-size: 13px;
        background: #D7D7D7;
        box-sizing: border-box;
        /* Shadow */

        box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.08);
        border-radius: 3px;
        padding: rem(10) rem(25);
        color:  #303030;
        margin: 0 rem(6);
        display: block;
        @include media-breakpoint-down(lg) {
            padding: 10px;
        }
        @include media-breakpoint-down(xs) {
            font-size: 12px;
        }
        &:hover {
            background: #c7c5c5;
        }
    }

    .nav-apart.active {
        background: #C4AB54;
        color: #fff;
    }

    .nav-tabs {
        border: none;
        margin: 0;
    }
    .nav.nav-pills {
        padding: 0;
        margin: 0;
        margin-bottom: -3px;
    }
    .nav-item {
        padding-top: 0;
        padding-bottom: 3px;
        margin-bottom: 0;
    }
}
.b-apart-plan__add {
    border-left: 1px solid #D7D7D7;
    height: 100%;
    padding: g(10) g(10);
    display: flex;
    flex-direction: column;
}
.b-apart-plan__item-text {
    color: #C4AB54;
    text-decoration: underline;
}
.b-apart-plan__item-link {
    text-decoration: none;
    color: #C4AB54;
}

.b-apart-plan__content {
    border-radius: 3px 0px 0px 3px;
    padding: g(10) g(10);
}
.b-apart-plan__nav {
    display: flex;
    justify-content: center;
}
.b-apart-plan__top {
    margin-bottom: g(10);
}
.b-apart-plan__left {
    display: flex;
    flex-direction: column;
    margin-right: 30px;
    align-items: flex-start;
}

.b-apart-plan__plan {
    text-align: center;
    margin-bottom: g(7);
}

.b-apart-plan__sale {
    color: #FFFFFF;
    font-weight: 600;
    font-size: rem(21px);
    background: #C4AB54;
    opacity: 0.8;
    border-radius: 3px;
    padding: rem(13) rem(15);
    display: inline-block;
}

.b-apart-plan__type {
    font-family: $SFProDisplay;
    font-size: 12px;
    line-height: 1;
    background: #C4AB54;
    color: #fff;
    border-radius: 3px;
    padding: g(4) g(6);
    display: block;
    display: inline-block;
    margin-bottom: g(6);
    text-align: left;
    align-self: flex-start;
}

.b-apart-plan__rooms-text {
    color: #1E1E1E;
    font-family: $Montserrat;
    font-weight: 500;
    font-size: remm(24);
}

.b-apart-plan__price {
    display: flex;
    align-items: center;
    text-align: left;
    padding-bottom: g(6);
}

.b-apart-plan__new-price {
    color: #363636;
    font-weight: normal;
    font-size: rem(27);
    margin-right: rem(18);
    &_primary {
        background: #C4AB54;
        color: #fff;
        padding: g(1) g(4);
    }
}

.b-apart-plan__old-price {
    text-decoration: line-through;
    color: #363636;
    font-weight: normal;
    font-size: rem(20);
}

.b-apart-plan__item {
    color: #1C1C1C;
    font-weight: 500;
    font-size: 15px;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    padding-bottom: g(8);

    span {
        font-weight: normal;
        margin-left: 3px;
        display: inline-block;
        white-space: nowrap;
    }
}

.b-apart-plan__desc {
    color: #343434;
    font-size: 15px;
    line-height: 1.6;
    margin-bottom: g(9);
}

.b-apart-plan__btn {
    @include btn;
    @include btn-yellow;
    @include btn-block;
    margin-bottom: g(6);
}

.b-apart-plan__social {
    display: flex;
    align-items: center;

    @include margin-bottom-not-last(g(6))
}

.b-apart-plan__social-text {
    color: #1C1C1C;
    font-weight: 500;
    font-size: 14px;
    padding-right: rem(13);
}

.b-apart-plan__rooms {
    display: flex;
    justify-content: space-between;
    margin-bottom: g(7);
}

.b-apart-plan__rooms-text {
    color: #1E1E1E;
    font-family: Montserrat;
    font-weight: 500;
    font-size: rem(24px);
}

.b-apart-plan__icon {
    svg {
        width: 23px;
        height: 23px;
        border-radius: 5px;
    }
    padding-right: rem(8);

    &:hover {
        fill: #C4AB54;
    }
}

.b-apart-plan__link {
    color: #114034;
    font-weight: 500;
    font-size: 14px;
    text-decoration-line: underline;
    @include link-h;
}



.b-apart-plan__img {
    img {
        max-height: 480px;
        height: 70vh;
        object-fit: contain;
        @include media-breakpoint-down(sm) {
            max-height: 70vh;
            height: auto;
        }
    }
}
.b-apart-plan__area-img {
    img {
        max-width: 170px;
        max-height: 115px;
    }
}

.b-apart-plan__rooms-fav {
    cursor: pointer;
    transition: 0.2s;
    fill: #121212;

    &:hover {
        fill: #C4AB54;
    }
}

.b-apart-plan__view {
    margin-top: auto;
}
.b-apart-plan__compass {
    position: relative;
    display: inline-block;
    transform: rotate(var(--degree));
    margin-bottom: g(10);
}
.b-apart-plan__compass-label {
    position: absolute;

    line-height: 0.8;

    width: 25px;
    height: 25px;
    font-size: 12px;
    color: #969393;
    font-weight: 500;
    display: flex;
    align-items: center;
    justify-content: center;
    &_top {

        bottom: 100%;
        left: 50%;
        transform: translateX(-50%) rotate(var(--degree-l));
    }
    &_bottom {

        top: 100%;
        left: 50%;
        transform: translateX(-50%) rotate(var(--degree-l));
    }
    &_left {

        top: 50%;
        right: 100%;
        transform: translateY(-50%) rotate(var(--degree-l));
    }
    &_right {

        top: 50%;
        left: 100%;
        transform: translateY(-50%) rotate(var(--degree-l));
    }
}
.b-apart-plan__compass-img {
    img {
        display: block;
        max-width: 30px;
        max-height: 30px;
    }
}
.b-apart-plan__plan-img {
    text-align: start;
}
.b-apart-plan__info-items {
    @include margin-bottom-not-last(g(10))
}
.b-apart-plan__info-item {
    display: grid;
    align-items: center;
    grid-template-columns: repeat(3, minmax(0, 1fr));
    gap: 10px;
    @include margin-bottom-not-last(g(6))
}
.b-apart-plan__add-top {
    @include margin-bottom-not-last(g(10))
}
.b-apart-plan__info-content {
    grid-column: auto / span 2;
    display: flex;
    align-items: flex-end;
}
.b-apart-plan__info-ico {
    display: flex;
    svg {
        flex-shrink: 0;
        margin-right: g(5);
    }
}
.b-apart-plan__info-name {
    line-height: 0.9;
    font-weight: 600;
    font-size: 14px;
    font-family: $Montserrat;
    color: #000;
}
.b-apart-plan__info-col-btn {

}
.b-apart-plan__info-btn {
    @include btn;
    @include btn-green;
    @include btn-small;
    @include btn-block;

}
.b-apart-plan__add-bottom {
    margin-top: auto;
}
@include media-breakpoint-down(md) {
    .b-apart-plan__view {
        display: flex;
        align-items: flex-start;
    }
    .b-apart-plan__compass {
        flex-shrink: 0;
    }
    .b-apart-plan__content {
        border-bottom: 1px solid #D7D7D7;
        border-right: none;
    }
    .nav-apart {
        margin-right: rem(10);
    }
    .b-apart-plan__sale {

        margin-right: rem(10);
    }
    .nav {
        padding-left: rem(30px);
    }
    .nav-item {
        margin-bottom: 15px;
    }
    .b-apart-plan__area-img {
        img {
            max-width: rem(100);
            margin-left: g(12);
        }
    }
    .b-apart-plan__img {
        img {
            margin-bottom: g(9);

        }
    }


}
@include media-breakpoint-down(sm) {
    .b-apart-plan {
        margin-top: g(12);
    }
    .b-apart-plan__sale {
        margin-bottom: 15px;
    }
    .b-apart-plan__add {
        border-left: none;
    }

    .b-apart-plan__top-row {
        //flex-direction: column-reverse;
    }
}
@include media-breakpoint-down(xs) {
    .b-apart-plan__add {
        border: 1px solid #D7D7D7;
    }
    .b-apart-plan__content {
        padding: 0;
        padding-bottom: 30px;
        border: none;
    }
    .b-apart-plan {
        border: none;
    }
    .b-apart-plan__sale {
        display: none;
    }
    .b-apart-plan__info-item {
        grid-template-columns: repeat(1, minmax(0, 1fr));
    }
    .b-apart-plan__info-content {
        grid-column: 1 / 2;
        justify-content: center;
    }
    .b-apart-plan__btn {
        margin-bottom: g(12);
    }
    .b-apart-plan__add-top {
        @include margin-bottom-not-last(g(12))
    }
}
