
.b-print {
    background: #eee;
}
.b-print__page {
    width: 210mm;
    height: 297mm;
    background: #fff;
    margin: 0 auto;
    position: relative;
    padding: 50px 50px 35px;
    display: flex;
    flex-direction: column;
    font-family: $Montserrat;
    &:after {
        content: '';
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        border: 8px solid var(--primary);
        pointer-events: none;
    }
}
.b-print__head {
    margin-bottom: 44px;
}
.b-print__logo {
    img {
        max-width: 347px;
    }
}
.b-print__tel {
    display: block;
    text-align: right;
    font-family: $Montserrat;
    font-size: 16px;
    color: #000;
    font-weight: 600;
    letter-spacing: 0.02em;
    margin-bottom: 4px;
}
.b-print__link {
    display: block;
    text-align: right;
    font-family: $Montserrat;
    font-size: 15px;
    color: #000;
    font-weight: 600;
}
.b-print__plan {
    padding-bottom: 57%;
    position: relative;
    margin-bottom: 38px;
    img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: contain;
    }
}
.b-print__floor-plan {
    img {
        max-height: 118px;
    }
}
.b-print__content {

}
.b-print__content-row {

}
.b-print__group {
    @include margin-bottom-not-last(42px);
}
.b-print__group-title {
    font-weight: 600;
    font-size: 12px;
    margin-bottom: 16px;
}
.b-print__col-compass {
    display: flex;
    flex-direction: column;
}
.b-print__compass {
    zoom: 0.5;
    img {
        transform: rotate(var(--degree));
        width: 35px;
    }
}
.b-print__area-plan {
    margin-top: auto;
    img {
        max-width: 84px;
        max-height: 75px;
    }
}
.b-print__text-area {
    font-family: $Montserrat;
    letter-spacing: 0.15em;
    word-wrap: break-word;
    font-size: 12px;
    line-height: 2.4;
}
.b-print__desc {
    color: #636066;
    font-size: 10px;
    //font-weight: 600;
    margin-bottom: 20px;
}
.b-print__yell {
    background: var(--secondary);
    padding: 24px 20px;
}
.b-print__name {
    font-weight: 600;
    font-size: 18px;
    margin-bottom: 10px;
    min-height: 52px;
}
.b-print__skills {
    margin-bottom: 40px;
}
.b-print__slill-item {
    display: flex;
    font-weight: 600;
    @include margin-bottom-not-last(19px);
}
.b-print__skill-name {
    margin-right: 5px;
}
.b-print__skill-val {
    margin-left: auto;
}
.b-print__text {
    font-size: 12px;
    font-weight: 600;
    line-height: 1.3;
    letter-spacing: 0.08em;
}
.b-print__footer {
    margin-top: auto;
    .row {
        align-items: flex-end;
    }
}
.b-print__footer-text {
    font-size: 12px;
    letter-spacing: 0.02em;
    font-family: $Montserrat;
    font-weight: 600;
    &_tar {
        text-align: right;
    }
}
.b-print__logo-4d {
    text-align: right;
    line-height: 1;
    img {
        max-width: 58px;
    }
}
.b-print__logo-4d-row {
    @include gutter(4);
}
