.b-developer-item {
    border: 1px solid #C4AB54;
    box-sizing: border-box;
    border-radius: 3px;
    height: 100%;
    background: #fff;
    overflow: hidden;
}
.b-developer-item__desc {
    padding: rem(20) rem(30) rem(40) rem(30);
}
.b-developer-item__img {
    img {
        width: 100%;
    }
    margin-bottom: g(3);
}
.b-developer-item__title {
    color: #1E1E1E;
    font-family: $Montserrat;
    font-weight: 500;
    font-size: rem(18px);
    margin-bottom: g(7);
}
.b-developer-item__text {
    color: #343434;
    font-weight: normal;
    font-size: 15px;
    line-height: 1.6;
}
@include media-breakpoint-down(sm) {
    .b-developer-item__title {
        font-size: 17px;
    }
    .b-developer-item__text {
        font-size: 15px;
    }
}
@include media-breakpoint-down(xs) {
    .b-developer-item__title {
        font-size: 16px;
    }
    .b-developer-item__text {
        font-size: 14px;
    }
}
