.b-advantage {
    background: #E5E5E5;
    padding: g(12) 0;
}

.b-advantage__desc {
    max-width: rem(550);
}

.b-advantage__img {
    padding-bottom: 150%;
    @include cover;
    border-radius: 3px;
}
@include media-breakpoint-down(md) {
    .b-advantage__desc {
        max-width: 100%;
    }

    .b-advantage {
        padding-top: g(11);
    }
    .b-advantage {
        padding: g(12) 0;
    }
}
@include media-breakpoint-down(sm) {
    .b-advantage__desc {
        margin-bottom: g(10);
    }
}
@include media-breakpoint-down(xs) {
    .b-advantage__row {
        @include gutter(5);
    }
    .b-advantage {
        //padding: g(10) 0;
    }
    .b-advantage__desc {
        max-width: 100%;
    }
    .b-advantage__img-row {
        @include gutter(5);
    }
}

