.b-repairs-slider {
    text-align: center;

    .swiper-button-prev,
    .swiper-button-next {
        position: absolute;
        top: 50%;
        margin-top: rem(-18px);
        z-index: 10;
        cursor: pointer;
        align-items: center;
        display: flex;
        justify-content: center;
        background: rgba(255, 255, 255, 0.67);
        border-radius: 50%;
        width: 37px;
        height: 37px;
    }
    .swiper-pagination-bullet-active {
        background: #C4AB54;
    }
    .swiper-pagination-bullet {
        width: 10px;
        height: 10px;
    }

    .swiper-button-prev {
        transform: rotate(180deg);
    }

}

.b-repairs-slider__slide {
    padding-bottom: 45%;
    @include cover;
}
.b-repairs-slider__desc {
    text-align: start;
    padding: rem(40) rem(320) 0 rem(90);
}
.b-repairs-slider__title {
    margin-bottom: g(8);
    color: #1F1F1F;
    font-family: $Montserrat;
    font-weight: 500;
    font-size: rem(24px);
}
.b-repairs-slider__text {
    color: #343434;
    font-weight: normal;
    font-size: 15px;
    line-height: 1.6;
    @include text-last-mb-fix;
}

@include media-breakpoint-down(md) {
    .b-repairs-slider__slide {
        padding-bottom: 80%;
    }
    .b-repairs-slider {
        //padding-bottom: g(12);
    }
    .b-repairs-slider__desc {
        padding: rem(50) 0 0 0;
    }
}

@include media-breakpoint-down(xs) {
    .b-repairs-slider__text {
        font-size: 14px;
    }
}
