.b-realt-apart__top {

    margin-bottom: g(11);
}
.b-realt-apart__top-right {
    display: flex;
    align-items: center;
}
.b-realt-apart__checkbox {
    //margin-right: g(10);
}
.b-realt-apart__top-row {
    display: grid;
    grid-template-columns: 1fr;
    grid-auto-columns: max-content;
    gap: 30px;
    grid-auto-flow: column;
    align-items: center;
}
.b-realt-apart__title {
    color: #1F1F1F;
    font-weight: 500;
    font-size: 25px;
}
.b-realt-apart__save {
    color: #1F1F1F;
    font-size: 16px;
    display: flex;
    align-items: flex-start;
    @include link-h;
}
.b-realt-apart__link {
    @include btn;
    @include btn-transparent;
    display: flex;
    flex-direction: row;
    align-items: center;
}
.b-realt-apart__row {
    @include gutter-y;
}
.b-realt-apart__col  {
    //margin-bottom: g(8);
}
.b-realt-apart__more {
    text-align: center;
    margin-top: g(10);
}
.b-realt-apart__more-btn {
    @include btn;
    @include btn-transparent;

}
@include media-breakpoint-down(md) {
    .b-realt-apart__col {
        &_plan {
            display: none;
        }
    }

}
@include media-breakpoint-down(sm) {
    .b-realt-apart__checkbox {
        //display: none;
        line-height: 1;
    }
    .b-realt-apart__row {
        @include gutter(5);
        @include gutter-y(5);
    }
    .b-realt-apart__top-row {
        grid-template-columns: repeat(2, 1fr);
        grid-auto-columns: 1fr;
        grid-auto-flow: unset;
        gap: 30px 5px;
        align-items: end;
    }
    .b-realt-apart__save {
        @include btn;
    }
    .b-realt-apart__col {
        &_save {
            grid-column: auto /span 2;
        }
    }
    .b-realt-apart__top {
        margin-bottom: 30px;
    }

}
@include media-breakpoint-down(xs) {
    .b-realt-apart__save {
        font-size: 14px;
    }
}
@media (max-width: 350px) {
    .b-realt-apart__top-row {
        grid-template-columns: repeat(1, 1fr);
    }
    .b-realt-apart__col {
        &_check-all {
            order: 99;
        }
    }
    .b-realt-apart__col {
        &_save {
            grid-column: auto /span 1;
        }
    }
}
