.b-dev-video__bg {
    position: relative;
    padding: g(12) 0;
    &:before {
        content: '';
        position: absolute;
        top: 0;
        bottom: 0;
        left: 300px;
        right: 0;
        background-image: url(../img/general/detail-bg3.png);
        background-position: top center;
        background-repeat: no-repeat;

    }
}
.b-dev-video__video {
    height: rem(378px);
}
.b-dev-video__title {
    color: #1F1F1F;
    font-family: $Montserrat;
    font-weight: 500;
    font-size: rem(24);
    margin-bottom: g(9);
}
.b-dev-video__list {
    color: #343434;
    font-size: 15px;
    font-weight: normal;
    ul {
        padding-left: 1em;
        margin-top: 0;
    }
    li {
        line-height: 1.6;
        margin-bottom: g(4);
    }
    @include text-last-mb-fix;
}
.b-dev-video__row {
    align-items: center;
}
@include media-breakpoint-down(md) {
    .b-dev-video__video {
        margin-bottom: g(10);
    }
    .b-dev-video {
        &:before {
            display: none;
        }
    }
}
