.b-advantages__item {
    border: 1px solid #B9B9B9;
    box-sizing: border-box;
    box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.08);
    border-radius: 3px;
    padding: rem(20px) rem(20px) rem(20px) rem(25px);
    height: 100%;
}
.b-advantages {
    height: 100%;
    background: #fff;
}
.b-advantages__item-title {
    color: #C4AB54;
    font-family: $Montserrat;
    font-weight: 500;
    font-size: rem(18px);
    margin-bottom: g(5);
}
.b-advantages__item-text {
    color: #343434;
    font-weight: normal;
    font-size: 15px;
    line-height: 1.6;
}
@include media-breakpoint-down(md) {
    .b-advantages__item-title {
        font-size: 18px;
    }
}
