
.b-tags {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin: -6px;

}
.b-tags__item {

}
.b-tags__link {
    display: block;
    padding: g(5) g(7);
    background: #D7D7D7;
    border-radius: 3px;
    color: #303030;
    font-size: 13px;
    margin: 6px;
    &:hover {
        background: #c0c0c0;
    }
}
.b-tags__link_current {
    background: #C4AB54;
    color: #fff;
}
@include media-breakpoint-down(md) {
    .b-tags {
        flex-wrap: nowrap;
        overflow: auto;
        padding-bottom: 10px;
    }
}
