.b-desc {
    max-width: rem(700px);
}
.b-desc__title {
    color: #1F1F1F;
    font-family: $Montserrat;
    font-weight: 500;
    font-size: rem(24px);
    margin-bottom: g(8);
}

.b-desc__text {
    color: #343434;
    font-family: $SFProDisplay;
    font-size: 15px;
    line-height: 1.7;
    &>*:last-child {
        margin-bottom: 0;
    }
}
.b-desc__place {
    margin-bottom: g(12);
}
.b-desc__icon {
    margin-bottom: g(9);
    max-width: rem(90);
}
@include media-breakpoint-down(sm) {
    .b-desc__text {
        margin-bottom: 0;
    }
}


