.b-realt-form {

}
.b-realt-form__form {
    width: 820px;
    margin: 0 auto;
    background: #F9F9F9;
    border: 1px solid #C8E7DF;
    box-sizing: border-box;
    box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.08);
    border-radius: 3px;
    padding: g(9) g(10) g(10);
}

.b-realt-form__title {
    color: #1F1F1F;
    font-family: $Montserrat;
    font-weight: 500;
    font-size: rem(24px);
    margin-bottom: g(10);
}
.b-realt-form__label {
    color: #2B2B2B;
    font-family: $Lato;
    font-weight: normal;
    font-size: 14px;
    display: flex;
    flex-direction: column;
}
.b-realt-form__col {
    margin-bottom: g(6);
}
.b-realt-form__form-control {
    padding: g(6) g(6);
    border: 1px solid #CDCDCD;
    box-sizing: border-box;
    border-radius: rem(13px);
    margin-top: g(7);
}
.b-realt-form__btn{
    @include btn;
    @include btn-yellow;
    //@include btn-block;
    padding: rem(18) rem(44);
    margin-top: g(8);
}
@include media-breakpoint-down(md) {
    .b-realt-form__form {
        width: 100%;
    }
    .b-realt-form__form-control {
        //width: 100%;
        //height: rem(53);
        margin-top: 10px;
    }
    .b-realt-form__label {
        //font-size: 16px;
    }
    .b-realt-form__btn{
        //padding: rem(25) rem(44);
    }
    .b-realt-form__col {
        margin-bottom: g(10)
    }
}
