.b-options-buy {
    flex-grow: 1;
    display: block;
}

.page__b-options-buy {
    background: #F9F9F9;
    padding-top: g(12);
    padding-bottom: g(11);
    margin-bottom: g(11);
}

.b-options-buy__item {
    display: flex;
    flex-direction: column;
    height: 100%;
    background: #FFFFFF;
    border: 1px solid #C4AB54;
    box-sizing: border-box;
    border-radius: 3px;
    padding: rem(21) rem(15) rem(19) rem(15);
}

.b-options-buy__title {
    color: #1F1F1F;
    font-family: $Montserrat;
    font-weight: 500;
    font-size: rem(24px);
    margin-bottom: g(11);
    text-align: center;
}

.b-options-buy__item-name {
    color: #1E1E1E;
    font-family: $Montserrat;
    font-weight: 500;
    font-size: rem(20px);
    margin-bottom: g(6);
}

.b-options-buy__item-text {
    color: #343434;
    font-size: 15px;
    padding: 0;
    @include margin-bottom-not-last(g(9));


    &>* {
        &:last-child {
            margin-bottom: 0;
        }
    }

    ul {
        padding-left: 1em;
    }
}

.b-options-buy__btn {
    @include btn;
    @include btn-yellow;
    width: 100%;
    margin-top: auto;
}
.b-options-buy_big{
    .b-options-buy__item{
        padding: rem(30);
    }
    .b-options-buy__btn{
        width: auto;
    }
    .b-options-buy__item{
        align-items: flex-start;
    }
    .b-options-buy__item-text{
        color: #333333;
    }
}

@include media-breakpoint-only(lg) {
    .b-options-buy__item-text {
        font-size: 16px;
    }
}
@include media-breakpoint-down(md) {
    .b-options-buy__item {
        max-width: 100%;
    }
    .b-options-buy__item-name {
        font-size: rem(24px);
    }


}
@include media-breakpoint-down(xs){
    .b-options-buy_big{
        .b-options-buy__item{
            padding: 15px;
            align-items: center;
        }
    }
}
