.b-options-apart {
    text-align: center;
    padding: g(12) 0;
}
.b-options-apart__row {

}
.b-options-apart__col {
    display: flex;
}
.b-options-apart__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: g(10);
}
.b-options-apart__title {
    color: #1F1F1F;
    font-weight: 500;
    font-size: rem(25px);
    max-width: 45%;
    text-align: left;
}
.b-options-apart__link-content {
    @include media-breakpoint-up(md) {
        @include btn;
        @include btn-transparent;
        //padding: g(5) g(10);

        display: flex;
        flex-direction: row;
        align-items: center;
    }
    .icon__print {
        fill: #fff;
    }
    svg {
        width: 1.2em;
        height: 1.2em;
    }
    .b-options-apart__icon {
        padding: 0;
        margin-right: 10px;
        display: inline-flex;
    }
}
.b-options-apart__icons {
    margin: -5px;
}
.b-options-apart__icon {
    padding: 5px;
    display: inline-block;
    line-height: 1;
}


.b-options-apart__more {
    @include btn;
    @include btn-transparent;
    margin-top: g(10);
}
.b-options-apart__row {
    margin-bottom: -30px;
}
.b-options-apart__item {
    margin-bottom: 30px;
    flex-grow: 1;
}
@include media-breakpoint-down(sm) {
    .b-options-apart__row {
        @include gutter(5);
    }
    .b-options-apart__row {
        @include gutter(5);
    }
    .b-options-apart__item {
        margin-bottom: 10px;
    }
    .b-options-apart__top {
        //display: block;
    }
    .b-options-apart__title {
        //margin-bottom: g(10);
    }
    .b-options-apart__link {
        font-size: 14px;
        display: flex;
        align-items: center;
        line-height: 1;
        color: #303030;
        &:after {
            content: '';
            content: '';
            display: inline-block;
            width: 7px;
            height: 13px;
            background: url(../img/general/nav.svg);
            background-size: 100% 100%;
            margin-left: g(4);
            transform: scale(1, -1);

        }
    }
}
.b-options-apart__social {
    display: flex;
    align-items: center;
}

.b-options-apart__social-text {
    color: #1C1C1C;
    font-weight: 500;
    font-size: 14px;
    padding-right: rem(13);
}
.b-options-apart__icon {
    svg {
        width: 23px;
        height: 23px;
        border-radius: 4px;
    }
    //padding-right: rem(8);

    &:hover {
        fill: #C4AB54;
    }
}
.b-options-apart__logo {
    display: none;
}
@include media-breakpoint-down(md) {
    .b-options-apart__link-content {
        //display: none;
    }
    .b-options-apart__social-text {
        //font-size: 18px;
        padding-right: rem(18);
    }
    .b-options-apart__icon {
        svg {
            //width: 30px;
            //height: 30px;
        }

        padding: 5px;
    }
    .b-options-apart__social-icons {
        margin: -5px;
    }
}
@include media-breakpoint-down(sm) {
    .b-options-apart__title {
        //display: none;
    }
}
@include media-breakpoint-down(xs) {
    .b-options-apart__social {
        flex-direction: column;
        align-items: flex-start;
    }
    .b-options-apart__social-text {
        margin-bottom: 8px;
    }
}
