.b-senks {
    color: #58534E;
}

.b-senks__title {
    font-family: $SFProDisplay;
    font-weight: 700;
    font-size: rem(30);
    margin-bottom: rem(12);
    text-transform: uppercase;
}

.b-senks__text {
    font-size: rem(20);
    margin-bottom: rem(24);
}

.b-senks__tel {
    font-size: 22px;
    display: block;
    color: #193348;
    span {
        font-weight: 300;
        color: #193348 !important;
    }
    &:hover {
        color: #D6B37E;
    }
}

.modal-body:not(.modal-body_light),
.b-form-section {
    .b-senks * {
        color: #fff !important;
    }
}
