.b-news-inner {

}
.b-news-inner__content {
    max-width: 874px;
    margin: 0 auto;
}
.b-news-inner__title {
    color: #1F1F1F;
    font-family: $Montserrat;
    font-weight: 500;
    font-size: rem(24px);
    margin-bottom: g(9);
}
.b-news-inner__text {
    color: #343434;
    font-weight: normal;
    font-size: 15px;
    &>*:last-child {
        margin-bottom: 0;
    }

}
@include media-breakpoint-down(lg) {
    .b-news-inner {
        width: 80%;

    }
}
@include media-breakpoint-down(md) {
    .b-news-inner {
        width: 100%;

    }
}
