
.b-about {
    background-color: #fff;
}

.b-about__content {
    max-width: rem(900);
    margin: 0 auto;
}


.b-about__title {
    color: #114034;
    font-family: $Montserrat;
    font-weight: 500;
    font-size: rem(24px);
    margin-bottom: g(8);
}

.b-about__desc {
    color: #343434;
    font-family: $SFProDisplay;
    font-size: 15px;
    line-height: 1.6;
}


.b-about__before {
    position: relative;
    padding: g(12) 0;
    &:before {
        content: '';
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        background-image: url(../img/general/detail-bg2.png);
        background-position: bottom right;
        background-repeat: no-repeat;

    }
}
.b-about__btn {
    @include btn;
    @include btn-yellow;
    margin-bottom: g(11);
}
@include media-breakpoint-down(md) {
    .b-about__before {
        &:before {
            display: none;
        }
    }
}
