.b-choice {
    padding: g(11) 0;
    background: #EFEFEF;
}
.b-choice__label {
    color: #1A1A1A;
    font-family: $SFProDisplay;
    font-size: 15px;
    margin-bottom: g(9);
}

.b-choice__btn {
    @include btn;
    @include btn-yellow;
}


@include media-breakpoint-down(lg) {
    .b-choice__group {
        margin-bottom: g(10);
    }
    .b-choice__label {
        //font-size: 12px;
        margin-bottom: g(7);
    }
    .b-choice__btn {
        @include btn-block;
    }
    .b-choice__filter-row {
        //align-items: flex-end;
    }
    .b-choice__col-btn {
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        //&_btn {
        //    height: 100%;
        //    display: flex;
        //    align-items: flex-end;
        //}
    }
}

@include media-breakpoint-down(sm) {
    .b-choice__btn {
        width: 100%;
        margin-top: g(10);
    }
    .b-choice {
        padding: g(12) 0;
    }
}
