.b-quarter-about {
    //padding-bottom: g(12);
}
.b-quarter-about__title {
    font-family: $Montserrat;
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    color: #1F1F1F;
    margin-bottom: g(9);
    b,strong {
        color: #C4AB54;
        font-weight: 400;
    }
}
.b-quarter-about__text {
    font-size: 15px;
    color: #343434;
    line-height: 1.6;
    &>* {
        &:last-child {
            margin-bottom: 0;
        }
    }
}
.b-quarter-about__row {
    align-items: center;
    @include gutter-y(30);
}
.b-quarter-about__img {
    img {
        border: 1px solid #D7D7D7;
        box-sizing: border-box;
        border-radius: 3px;
        width: 100%;
    }
}
@include media-breakpoint-down(sm) {
   
}
@include media-breakpoint-down(xs) {
    .b-quarter-about__title {
        font-size: 18px;
    }
    .b-quarter-about__text {
        font-size: 14px;
        line-height: 1.4;
    }

}
