.b-checkbox {
    color: #323232;
    font-family: $Karla;
    font-size: 16px;
    display: flex;
}
.checkbox-input {
    position: absolute;
    z-index: -1;
    opacity: 0;
}
.checkbox-input+span.b-checkbox__label,
.checkbox-input+label {
    display: inline-flex;
    user-select: none;
    line-height: 20px;
}

.checkbox-input+span.b-checkbox__label::before,
.checkbox-input+label::before {
    content: '';
    display: inline-block;
    width: rem(20);
    height: rem(20);
    flex-shrink: 0;
    flex-grow: 0;
    border: 1px solid #E0E0E0;
    border-radius: 3px;
    margin-right: rem(10);
    background-repeat: no-repeat;
    background-position: center center;
    background-size: 50% 50%;
    background-color: #fff;
}

.checkbox-input:not(:disabled):not(:checked)+span.b-checkbox__label:hover::before,
.checkbox-input:not(:disabled):not(:checked)+label:hover::before{
    border-color: #E0E0E0;
}

.checkbox-input:not(:disabled):active+span.b-checkbox__label::before,
.checkbox-input:not(:disabled):active+label::before{
    background-color: #FAFAFA;
    border-color: #E0E0E0;
}


.checkbox-input:checked+span.b-checkbox__label::before,
.checkbox-input:checked+label::before {
    background-image: url("../../static/img/general/check.svg");
}
@include media-breakpoint-down(md) {
    .b-checkbox {
        font-size: 18px;
    }
    .checkbox-input+label {
        line-height: rem(30);
    }
    .checkbox-input+span.b-checkbox__label::before,
    .checkbox-input+label::before {
        width: rem(30);
        height: rem(30);
        margin-right: rem(20);

    }
}



