.b-news-item {
    border: 1px solid #D7D7D7;
    box-sizing: border-box;
    border-radius: 3px 3px 0px 0px;
    height: 100%;
    display: flex;
    flex-direction: column;
}
.b-news-item__desc {
    padding: rem(20) rem(30) rem(30) rem(30);
    text-align: start;
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}
.b-news-item__title {
    color: #1E1E1E;
    font-family: $Montserrat;
    font-weight: 500;
    font-size: rem(20px);
    margin-bottom: g(7);
}
.b-news-item__text {
    color: #343434;
    font-weight: normal;
    font-size: 15px;
    margin-bottom: g(10);
    &>*:last-child {
        margin-bottom: 0;
    }
}
.b-news-item__btn {
    @include btn;
    @include btn-yellow;
    margin-top: auto;
}
.b-news-item__img {
    img {
        width: 100%;
    }
}
@include media-breakpoint-down(xs) {
    .b-news-item__title {
        font-size: 16px;
    }
    .b-news-item__text {

    }
}
