.b-rassrochka-subtitle {

}
.b-rassrochka-subtitle__name {
    font-family: $Montserrat;
    font-style: normal;
    font-weight: 700;
    font-size: rem(32);
    line-height: 1.2;
    color: #174034;
    @include margin-bottom-not-last(16px);
}
.b-rassrochka-subtitle__description {
    font-family: $Montserrat;
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 1.2;
    color: #A7A7A7;
}

@include media-breakpoint-down(md){
    .b-rassrochka-subtitle__name{
        font-size: 24px;
    }
}

@include media-breakpoint-down(sm){
    .b-rassrochka-subtitle__name{
        font-size: 22px;
    }
}

@media (max-width: 330px) {
    .b-rassrochka-subtitle__name{
        font-size: 18px;
    }
    .b-rassrochka-subtitle__description{
        font-size: 13px;
    }
}

