.b-inves {
    padding: g(12) 0;
    background: #F7F7F7;
    background-image: url(../img/general/bg-ico.png);
    background-position: bottom right;
    background-repeat: no-repeat;
    background-size: 50% auto;
}

.b-inves__desc {
    padding-top: g(11);
}
.b-inves__img {
    position: relative;
    display: block;
    img {
        width: 100%;
        display: block;
        border-radius: 3px;
    }
}
.b-inves__img-text{
    font-family: $Montserrat;
    font-weight: 500;
    font-size: rem(18px);
    position: absolute;
    color: #FFFFFF;
    background: rgba(17, 64, 52, 0.94);
    bottom: 15px;
    padding-top: rem(20px);
    text-align: center;
    padding-bottom: rem(20px);
    left: 0;
    right: 0;
    bottom: 0;
}

@include media-breakpoint-down(md) {
    .b-inves {
        padding: g(12) 0;
    }
    .b-inves__desc {
        padding-left: 0;
    }
    .b-inves__img {
        margin-bottom: g(6);
        img {
            width: 100%;
        }
    }
}
@include media-breakpoint-down(sm) {
    .b-inves {
        background-size: 100% auto;
    }
}
