

.b-photo-desc__title {
    color: #fff;
    font-family: $Montserrat;
    font-weight: 500;
    font-size: rem(20px);
}

.b-photo-desc__img {
    position: relative;

    img {
        width: 100%;
        display: block;
        border: 1px solid #D7D7D7;
        box-sizing: border-box;
        border-radius: 3px;
    }
}

.b-photo-desc__content {
    position: absolute;
    bottom: g(10);
    left: g(10);
    background: #114034;
    border-radius: 3px;
    padding: rem(30);
    display: inline-block;

}

.b-photo-desc {
    b, strong {
        color: #C4AB54;
        white-space: nowrap;
    }
}

.b-photo-desc__btn-link {
    @include btn;
    @include btn-yellow;
}

.desc-right {
    position: absolute;
    bottom: g(10);
    right: g(11);
    left: auto;
    max-width: 395px;
    .b-photo-desc__title {
        margin-bottom: g(8);
    }
}
@include media-breakpoint-down(md) {
    .page__b-photo-desc {
        //padding-bottom: g(11);
        //padding-top: g(10);
    }
}
@include media-breakpoint-down(sm) {
    .desc-right {
        bottom: g(10);
        left: g(10);
        right: g(10);
        width: auto;
    }
    .b-photo-desc__content {
        position: static;
        margin-top: 20px;
        display: block;
        max-width: none;
    }
}
