
.b-banners__grid {
    display: grid;
    gap: 1em;
    grid-template-columns: repeat(4, 1fr);
    grid-auto-rows: 1fr;
}

.b-banners__grid-item {
    background: #F5F5F5;
    border-radius: 3px;
    display: flex;
    flex-direction: column;
}

.b-banners__grid-item_1 {
    grid-column: auto / span 2;
    grid-row: auto / span 2;
    .b-banners__grid-content {
        padding-bottom: g(8);
    }
    .b-banners__title {
        font-size: rem(36px);
        @include media-breakpoint-down(md) {
            font-size: rem(25px);
        }
        @include media-breakpoint-down(sm) {
            font-size: 22px;
        }
    }
}
.b-banners__img {
    @include cover;
    background-position: top;
    flex-grow: 1;
}

.b-banners__grid-item_2 {
    grid-column: auto / span 2;
    .b-banners__title {
        font-size: rem(30px);
        @include media-breakpoint-down(md) {
            font-size: 22px;
        }
    }
}
.b-banners__grid-content {
    padding: g(11);
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    &_h100 {
        min-height: 100%;
    }
}
.b-banners__title-cont {
    margin-bottom: g(12);
}
.b-banners__title {
    font-weight: 600;
    font-size: rem(24px);
    color: #000000;

    font-family: $Montserrat;
    @include margin-bottom-not-last(g(5));
    @include media-breakpoint-down(md) {
        font-size: 22px;
    }
}
.b-banners__sub-title {
    font-family: $Montserrat;
    font-weight: 300;
    font-size: rem(24px);
    color: #000000;
}
.b-banners__btn {
    @include btn;
    @include btn-green;
    @include btn-sm-small;
    margin-top: auto;

}
@include media-breakpoint-down(lg) {
    .b-banners__title-cont {
        margin-bottom: g(10);
    }
    .b-banners__grid {
        grid-template-columns: repeat(2, 1fr);
    }
    .b-banners__grid-item {
        grid-column: auto / span 1;
        grid-row: auto / span 1;
        &_1 {
            grid-row: auto / span 3;
        }
    }
    .b-banners__title {
        @include margin-bottom-not-last(g(9));
    }
}
@include media-breakpoint-down(sm) {
    .b-banners__grid {
        grid-template-columns: repeat(2, minmax(0, 1fr));
        grid-auto-rows: auto;
    }
    .b-banners__grid-item {
        grid-column: auto / span 1;
        &_1 {
            grid-column: auto / span 2;
        }
        &_2 {
            grid-column: auto / span 2;
        }
    }

    .b-banners__img {
        aspect-ratio: 1 / 0.6;
    }
    .b-banners__btn {
        @include btn-block;
    }
    .b-banners__top {
        width: 100%;
    }
    .b-banners__title {
        font-weight: 500;
        font-size: 15px;
        line-height: 1.2;
    }
}
