.b-callme {
    padding-top: g(12);
    padding-bottom: g(12);
    color: #fff;
    text-align: center;
    background: #114034;
    position: relative;

    &:before {
        content: '';
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        background-image: url(../img/general/call-bg.png);
        background-position: bottom right;
        background-repeat: no-repeat;
    }
}

.b-callme__content {
    align-items: center;
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.b-callme__text {
    font-family: $Montserrat;
    font-weight: 500;
    font-size: rem(24px);
    padding-right: rem(122);
}

.b-callme__btn {
    @include btn;
    @include btn-yellow;
}

.callme-bg {
    margin-bottom: g(12);
    padding-top: g(11);
    padding-bottom: g(11);
    color: #fff;
    text-align: center;
    background: #114034;
    position: relative;
    &:before {
        content: '';
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        background-image: none;
    }
    .b-callme__text {
        font-size: rem(20px);
        padding-right: rem(71);
        text-align: left;
    }
    .b-callme__btn {
        padding: rem(22) rem(99);
    }
}

@include media-breakpoint-down(md) {
    .b-callme {
        &:before {
            opacity: 0.8;
            background-size: auto 30px;
        }
    }
}

@include media-breakpoint-down(sm) {
    .b-callme__content {
        flex-direction: column;
    }
    .b-callme__text {
        padding: 0;
        margin-bottom: g(10);
    }
    .b-callme__btn {
        //padding-right: g(13);
        //padding-left: g(13);
    }
}
