.b-img-grid__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-auto-rows: 1fr;
}

.b-img-grid__img {
    @include cover;
    transition: 0.3s;
    position: relative;
    display: flex;


    &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        background: rgba(0, 0, 0, 0.6);
        opacity: 0;
        transition: 0.3s;
    }

    &:hover {
        &:after {
            opacity: 1;

        }
    }
}

.b-img-grid__desc {
    width: 100%;
    height: 100%;
    padding: g(10);
    z-index: 100;
    display: flex;
    position: relative;
    flex-direction: column;
    justify-content: flex-end;
    opacity: 0;

    &:hover {
        opacity: 1;
    }
}

.b-img-grid__title {
    color: #FFFFFF;
    font-weight: 500;
    font-size: 20px;
    margin-bottom: var(--g7);

}

.b-img-grid__text {
    color: #FFFFFF;
    @include list;
    li {
        padding-left: 1em;
        &:before {
            background: #fff;
            top: 0.45em;

        }
    }
}

@include media-breakpoint-down(lg) {
    .b-img-grid__grid {
        grid-template-columns: repeat(2, 1fr);
    }
    .b-img-grid__desc {
        opacity: 1;
    }
    .b-img-grid__img {
        &:after {
            background: rgba(0, 0, 0, 0.5);
            opacity: 1;
        }
    }
}

@include media-breakpoint-down(xs) {
    .b-img-grid__grid {
        grid-template-columns: repeat(1, 1fr);
    }
}
