.b-adress {
    background: #fff;
    border: 1px solid #D7D7D7;
    border-radius: 3px;
    flex-grow: 1;
    display: flex;
    flex-direction: column;
}
.b-adress__bottom {
    padding: g(10) g(10);
    flex-grow: 1;
    display: flex;
    flex-direction: column;
}
.b-adress__img {
    position: relative;
    height: 211px;
    img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        display: block;
    }
    &>ymaps {
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        height: 100%;
    }
}
.b-adress__title {
    color: #1E1E1E;
    font-family: $Montserrat;
    font-weight: 500;
    font-size: rem(24px);
    margin-bottom: g(9);
}
.b-adress__text {
    color: #1C1C1C;
    font-family: $SFProDisplay;
    font-weight: normal;
    font-size: 16px;
}
.b-adress__city {
    margin-bottom: g(3);
}
.b-adress__street {
    margin-bottom: g(7);
}
.b-adress__email {
    margin-bottom: g(7);
    display: block;
    color: #1C1C1C;
    @include link-h;
}
.b-adress__time {
    font-weight: 500;
    margin-bottom: g(10);
}
.b-adress__btn {
    @include btn;
    @include btn-yellow;
    width: 100%;
    margin-top: auto;
}
@include media-breakpoint-down(ld) {
    .b-adress {
        margin-bottom: g(10);
    }
}

@include media-breakpoint-down(sm) {
    .b-adress__text {
        font-size: 14px;
    }
}

