.b-team {
    text-align: center;
    margin-bottom: g(12);

}
.b-team__title {
    color: #1F1F1F;
    font-family: $Montserrat;
    font-weight: 500;
    font-size: rem(24px);
    margin-bottom: g(11);
}
.b-team__person {
    margin-bottom: 30px;
}
.b-team__row {
    margin-bottom: -30px;
}
@include media-breakpoint-down(xs) {
    .b-team__title {
        font-size: 18px;
    }
}
