.b-apartments-inner__title {
    color: #1F1F1F;
    font-weight: 500;
    font-size: rem(25px);
}

.b-apartments-inner__top {
    display: flex;
    justify-content: space-between;
    margin-bottom: g(10);
    align-items: center;
}
.b-apartments-inner__content {
    display: inline-block;
    line-height: 1;
    position: relative;
}

.b-apartments-inner__img {
    position: relative;
    display: inline-block;
    img {
        aspect-ratio: auto;
        height: auto !important;
    }
}
.b-apartments-inner__inner {
    max-width: 1100px;
    margin: 0 auto;
}
.b-apartments-inner__items {
    background: #FFFFFF;
    border: 1px solid #D7D7D7;
    box-sizing: border-box;
    border-radius: 3px;
    padding: 10px 20px;
    position: absolute;
    top: 30px;
    left: 35px;
    display: flex;
    align-items: center;
    z-index: 20;
}
.b-apartments-inner__items-links {
    margin: -5px;
}
.b-apartments-inner__items-link {
    color: #FFFFFF;
    font-weight: normal;
    font-size: 15px;
    background: #A7A7A7;
    box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.08);
    border-radius: 3px;
    padding: g(6);
    display: inline-block;
    margin: 5px;
    min-width: 38px;
    text-align: center;



    &:hover {
        background: #C4AB54;
    }

    &_active {
        background: #C4AB54;
    }
}

.b-apartments-inner__items-title {
    color: #1E1E1E;
    font-weight: 500;
    font-size: 16px;
    margin-right: g(10);
    white-space: nowrap;
}

.b-apartments-inner__add {
    ///display: inline-block;
    background: #FFFFFF;
    border: 1px solid #D7D7D7;
    box-sizing: border-box;
    border-radius: 3px;
    padding: 15px 23px 30px;
    position: absolute;
    bottom: 50px;
    left: 35px;
    display: none;
    animation: 0.2s top forwards;
    z-index: 30;
    pointer-events: none;
}

.b-apartments-inner__link {
    @include media-breakpoint-up(lg) {
        @include btn;
        @include btn-transparent;

        display: flex;
        flex-direction: row;
        align-items: center;
    }

}

.b-apartments-inner__add-title {
    color: #1E1E1E;
    font-weight: 500;
    font-size: 16px;
    margin-bottom: g(8);
}

.b-apartments-inner__add-item {
    color: #1C1C1C;
    font-weight: 500;
    font-size: 15px;
    display: flex;
    margin-bottom: g(4);

    &:last-child {
        margin-bottom: 0;
    }

    span {
        font-weight: normal;
        margin-left: g(3);
    }
}

.b-apartments-inner__svg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 15;
}
.b-apartments-inner__contour {
    opacity: 0.3;
    transition: 0.2s;
    fill: #C4AB54;
    stroke: #C4AB54;
    stroke-width: 2px;
    will-change: opacity;
    &:hover {
        opacity: 0.5;
    }
}
.b-apartments-inner__link_disabled {
    //pointer-events: none;
    cursor: auto;
    .b-apartments-inner__contour {
        fill: #114034;
        stroke: #114034;
        opacity: 0.8;
    }
}
.b-apartments-inner_floor {
    .b-apartments-inner__items {
        position: static;
        display: inline-flex;
        margin-bottom: g(10);
    }
}
.b-apartments-inner__back {
    display: flex;
    align-items: center;

    margin-bottom: 30px;
    color: #000;
    &:before {
        content: '';
        display: inline-block;
        width: 7px;
        height: 13px;
        background: url(../img/general/nav.svg);
        transform: scale(-1, -1);
        margin-right: 10px;
    }
}
@include media-breakpoint-down(md) {
    .b-apartments-inner__items {
        top: 0;
        left: 0;
        flex-direction: column;
    }
    .b-apartments-inner__add {
        display: none !important;
    }
    .b-apartments-inner__items-title {
        margin-right: 0;
        margin-bottom: g(8);
    }
    .b-apartments-inner__items {
        display: block !important;
        text-align: center;
        position: static;
    }
    .b-apartments-inner__link {
        display: flex;
        align-items: center;
        line-height: 1;
        color: #1E1E1E;
        font-size: 14px;
        &:after {
            content: '';
            content: '';
            display: inline-block;
            width: 7px;
            height: 13px;
            background: url(../img/general/nav.svg);
            background-size: 100% 100%;
            margin-left: g(4);
            transform: scale(1, -1);

        }
    }
}
