.b-mort-photo {
    &_text-big {
        .b-mort-photo__title {

            @include media-breakpoint-up(xl) {
                text-align: center;
                font-size: rem(24px);
                margin-bottom: g(11);
            }
        }
        .b-mort-photo__text {
            @include media-breakpoint-up(xl) {
                font-size: 18px;
            }
            @include list;
            li {
                padding-left: 1.6em;
                display: block;
            }
            li:before {
                width: 1.6em;
                height: 1.6em;
                transform: scale(0.25);
                top: 0;
            }
        }
    }
}

.b-mort-photo__content {
    border: 1px solid #D7D7D7;
    box-sizing: border-box;
    border-radius: 0px 3px 3px 0px;
    max-width: 1190px;
    margin: 0 auto;
}
.b-mort-photo__img {
    img {
        border-radius: 3px 0px 0px 3px;
        height: 100%;
        width: 100%;
        object-fit: cover;
    }
    height: 100%;
}
.b-mort-photo__desc {
    padding: g(10);
}
.b-mort-photo__title {
    color: #1C1C1C;
    font-family: $Montserrat;
    font-weight: 500;
    font-size: rem(20px);
    margin-bottom: g(7);
}
.b-mort-photo__text {
    color: #343434;
    font-weight: normal;
    font-size: 15px;
    p {
        margin-bottom: g(10);
        line-height: 1.6;
    }
    ul {
        padding-left: 1em;
    }
    li {
        line-height: 1.6;
    }
    @include text-last-mb-fix;
}
@include media-breakpoint-down(lg) {
    .b-mort-photo__content {
        max-width: 100%;
    }
    .b-mort-photo__desc {
        padding-top: g(11);
    }
}
@include media-breakpoint-down(md) {
    .b-mort-photo__desc {

    }
}
@include media-breakpoint-down(xs) {
    .b-mort-photo__title {
        font-size: 16px;
    }
    .b-mort-photo__text {
        font-size: 14px;
    }
}
