.b-more-btn {
    @include btn;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    fill: none;

}
.b-more-btn__text {

}


.b-more-btn__num {
    font-weight: bold;
    font-family: $SFProDisplay;
}
.b-more-btn__ico {
    flex-shrink: 0;
    margin-left: rem(20);
    svg {
        width: rem(21);
        height: rem(11);
    }
}
@include media-breakpoint-down(sm) {
    .b-more-btn {
        font-size: 14px;
        padding-left: 15px;
        padding-right: 15px;
    }
}

