.b-realt-doc__title {
    color: #1F1F1F;
    font-family: $Montserrat;
    font-weight: 500;
    font-size: rem(24px);
    margin-bottom: g(9);
}
.b-realt-doc__text {
    color: #343434;
    font-weight: normal;
    font-size: 15px;
}
.b-realt-doc__items-title {
    color: #1F1F1F;
    font-family: $Montserrat;
    font-weight: 500;
    font-size: rem(18px);
    margin-bottom: g(9);
}
.b-realt-doc__desc {
    margin-left: rem(110);
}
.b-realt-doc__col {
    margin-bottom: g(6);
}
.b-realt-doc__item {
    background: #F1F1F1;
    border: 1px solid #D7D7D7;
    border-radius: 3px;
    padding: g(8) g(8);
    height: 100%;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    &:hover {
        background: #C4AB54;
        color: #fff;
        .b-realt-doc__link {
            color: #fff;
        }

    }
}
.b-realt-doc__link {
    color: #1E1E1E;
    font-weight: 500;
    font-size: 14px;
}
@include media-breakpoint-down(md) {
    .b-realt-doc__title {
        font-size: 20px;
    }
    .b-realt-doc__text {
        font-size: 16px;
    }
    .b-realt-doc__link {
        font-size: 16px;
    }
    .b-realt-doc__item {
        padding: rem(30) rem(70);
    }
}
@include media-breakpoint-down(lg) {
    .b-realt-doc__desc {
        margin-left: 0;
    }
}
