.b-sale__link {
    font-family: $Montserrat;
    font-weight: 500;
    font-size: 17px;
    min-height: rem(253px);
    max-width: rem(253px);
    align-items: center;
    justify-content: center;
    background-color: #114034;
    border-bottom-left-radius: 110px;
    border-top-right-radius: 110px;
    padding: 34px;
}

.b-sale__link {
    display: flex;
    flex-direction: column;
}


.b-sale__title {
    color: #FFF;
    margin-bottom: g(9);
    text-align: center;
    line-height: 1.38;
}

.b-sale__text {
    color: #C4AB54;
}

.b-sale__button {
    font-family: $SFProDisplay;
    font-weight: 500;
    font-size: 15px;
    color: #fff;
    padding: rem(10) rem(17) rem(11) rem(17);
    border: rem(2px) solid #C4AB54;
    box-sizing: border-box;
    filter: drop-shadow(1px 1px 3px rgba(0, 0, 0, 0.08));
    border-radius: rem(3px);

}
.b-sale__add {
    font-weight: normal;
    font-size: rem(18px);
    color: #FFFFFF;
    margin-bottom: g(8);
    &:last-child {
        margin-bottom: 0;
    }
    span {
        color: #D4B655;
        margin-left: g(4);
    }
}
@include media-breakpoint-down(md) {
    .b-sale {
        text-align: right;
    }
}

@include media-breakpoint-down(sm) {
    .b-sale__button {
        padding-left: 6px;
        padding-right: 6px;
        text-align: center;
    }
    .b-sale__link {
        min-width: 220px;
        margin-left: auto;
    }
}

@include media-breakpoint-down(xs) {
    .b-sale__button {
        font-size: 12px;
        font-weight: 400;
    }
    .b-sale__link {
        font-size: 12px;
    }
}
