.b-num-input {
    font-family: $SFProDisplay;
}
.b-num-input__label {

    font-size: 14px;
    color: #7E7E7E;
    padding: 0 rem(19);
    margin-bottom: rem(6);
}
.b-num-input__border {
    border: 1px solid #D3D2D2;
    border-radius: 3px;
    padding: rem(9) rem(18);
    display: flex;
    align-items: center;
    background: #fff;
}
.b-num-input__btn {
    width: 18px;
    height: 18px;
    background: #CBA470;
    border-radius: 1px;
    position: relative;
    cursor: pointer;
    &:before,
    &:after {
        content: '';
        background: #FFFFFF;
        width: 10px;
        height: 2px;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        display: block;
    }
    &_plus {
        &:after {
            transform: translate(-50%,-50%) rotate(-90deg);
        }
    }
}

.b-num-input__input {
    width: 43px;
    text-align: center;
    font-size: 17px;
    line-height: 1;
    color: #434343;
    border: none;
    padding: 0;

}

.b-num-input__ico {
    margin-left: auto;
}

