
.b-office {
    padding: g(12) 0;
}
.b-office__address-col {
    display: flex;
}
.b-office__adress {
    margin-bottom: g(10);
}
.b-office__other {
    padding: g(10);
    border: 1px solid #D7D7D7;
    border-radius: 3px;
    background: #FFFFFF;
}
.b-office__other-row {
    margin-bottom: -30px;
}
.b-office__item {
    margin-bottom: 30px;
}
.b-office__other-title {
    color: #1E1E1E;
    font-family: $Montserrat;
    font-weight: 500;
    font-size: rem(24px);
    margin-bottom: g(9);
}
.b-office__other-name {
    font-family: $SFProDisplay;
    font-weight: 500;
    font-size: rem(16px);
    margin-bottom: g(5);
}
.b-office__other-tel {
    font-family: $SFProDisplay;
    font-weight: normal;
    font-size: rem(16px);
    color: #1E1E1E;
    @include link-h;
}

@include media-breakpoint-down(md) {
    .b-office__other-tel {
        margin-bottom: g(9);
        font-size: rem(20px);
    }
    .b-office__other-name {
        font-size: rem(20px);
        margin-bottom: g(5);
    }
}

@include media-breakpoint-down(xs) {
    .b-office__other-tel {
        font-size: 14px;
    }
    .b-office__other-name {
        font-size: 15px;
    }
}
