.b-photo-slider {
    @include tabs;
    @include tabs-scroll-sm;
    //padding-top: g(10);
}

.b-photo-slider__nav {
    margin-bottom: g(9);
}

.b-photo-slider__slider-top {
    position: relative;
}

.b-photo-slider__slider {
    position: relative;
    //margin-bottom: g(13);

    .swiper-slide-thumb-active {
        opacity: 0.7;
    }

    .swiper-button-prev,
    .swiper-button-next {
        position: absolute;
        top: 50%;
        margin-top: rem(-18px);
        z-index: 10;
        cursor: pointer;
        background: rgba(255, 255, 255, 0.67);
        border-radius: 50%;
        width: rem(37px);
        height: rem(37px);
        align-items: center;
        display: flex;
        justify-content: center;

        svg {
            width: 50%;
            height: 50%;
        }
    }

    .swiper-button-prev {
        transform: rotate(180deg);
    }
}

.b-photo-slider__slide {
    padding-bottom: 40%;
    @include cover;

    .tab-content {
        margin-top: g(9);
    }
}


.b-photo-slider__slider-thumbs {
    margin-top: g(9);
}

@include media-breakpoint-down(xs) {

    .b-photo-slider__slider {
        .swiper-button-prev,
        .swiper-button-next {
            background: transparent;
        }

        .swiper-button-prev, .swiper-container-rtl .swiper-button-next {
            left: 0;
        }

        .swiper-button-next, .swiper-container-rtl .swiper-button-prev {
            right: 0;
        }
    }
}
