.b-building {
    @include swiper-pagination-normal;
    text-align: center;

    .fade-enter-active {
        transition: opacity .5s;
    }

    .fade-enter, .fade-leave-to /* .fade-leave-active до версии 2.1.8 */
    {
        opacity: 0;
    }

    .swiper-button-prev,
    .swiper-button-next {
        position: absolute;
        top: 50%;
        margin-top: rem(-18px);
        z-index: 10;
        cursor: pointer;
        background: none;
        align-items: center;
        display: flex;
        justify-content: center;
        background: rgba(255, 255, 255, 0.67);
        border-radius: 50%;
        width: rem(37px);
        height: rem(37px);
        transition: 0.2s;

        &:hover {
            background: rgba(255, 255, 255, 0.8);
        }
    }

    .swiper-button-prev {
        transform: rotate(180deg);
    }
}

.b-building__white {
    text-align: start;
    padding: g(10) g(10) g(11);
}

.b-building__text {
    color: #343434;
    font-size: 16px;

    b, strong {
        font-weight: 500;
        font-size: 16px;
        margin-bottom: g(7);
    }

    & > * {
        margin-top: g(7);
    }

    @include text-last-mb-fix;
    margin-bottom: g(10);
}

.b-building__head-title {
    color: #1F1F1F;
    font-family: $Montserrat;
    font-weight: 500;
    font-size: rem(24px);
    margin-bottom: g(10);
}

.b-building__title {
    color: #343434;
    font-weight: 500;
    font-size: rem(19px);
    margin-bottom: g(9);
}


.b-building__nav-group_year {
    margin-bottom: g(8);
}


.b-building__nav-group_months {
    margin-bottom: g(10);
}

.b-building__nav-item {
    cursor: pointer;
    font-weight: normal;
    font-size: 13px;
    color: #000;
    display: inline-block;
    margin: 0 var(--g1);
    padding: g(5) g(6);
    user-select: none;
}

.b-building__nav-group_year {
    .b-building__nav-item {
        cursor: pointer;
        font-weight: normal;
        font-size: rem(20px);
        display: inline-block;
    }

    .b-building__nav-item_active {
        background: #C4AB54;
        border-radius: 3px;
        color: #fff;
    }
}

.b-building__nav-group_months {
    .b-building__nav-item {
        background: #D7D7D7;
        border-radius: 3px;
        color: #fff;
        margin: g(3);
        padding: g(5) g(6);
    }

    .b-building__nav-item_active {
        background: #C4AB54;
        border-radius: 3px;
        color: #fff;
    }
}

.b-building__slider {
    position: relative;
    height: 100%;
    //display: flex;
    .swiper-container {
        height: 100%;
        margin: 0;
    }

    .swiper-slide {
        height: 100%;
    }
}

.b-building__slide {
    display: block;
    height: 100%;
}

.b-building__img {
    display: block;

    @include cover;
    @include media-breakpoint-up(md) {
        padding-bottom: 56%;
        min-height: 100%;
    }
    @include media-breakpoint-down(sm) {
        padding-bottom: 80%;
    }
}

.b-building__btn {
    @include btn;
    @include btn-yellow;
}

.b-building__month-btn {
    cursor: pointer;

    &_disabled {
        opacity: 0.5;
        pointer-events: none;
    }
}

.b-building__btns {
    margin-bottom: g(10);
}

.b-building__month-btn_next {
    fill: #fff;
    background: #C4AB54;
    border-radius: 3px;
    display: inline-block;
    padding: rem(10) rem(13);
}

.b-building__month-btn_prev {
    fill: #fff;
    transform: rotate(180deg);
    background: #C4AB54;
    border-radius: 3px;
    display: inline-block;
    padding: rem(10) rem(13);
    margin-right: g(7);
}

.b-building__current {
    border: 1px solid #D7D7D7;
    box-sizing: border-box;
}

@include media-breakpoint-down(md) {
    .b-building__nav-group {
        overflow: auto;
        white-space: nowrap;
        padding-bottom: 10px;
    }
}

@include media-breakpoint-down(sm) {
    .b-building__current {
        //border: none;
    }
    .b-building__month-btn_next {
        padding: rem(20) rem(25);
    }
    .b-building__month-btn_prev {
        padding: rem(20) rem(25);
    }
    .b-building__nav-item {
        font-size: 16px;
        padding: rem(15) rem(25);
    }
    .b-building__title {
        font-size: 20px;
    }

    .b-building__btns {
        margin-bottom: g(12);
    }
    .b-building__nav-group_year {
        margin-bottom: 0;
    }

}

@include media-breakpoint-down(xs) {
    .b-building__text {
        font-size: 14px;
    }
}
