.b-mortgage {
    padding: g(12) 0;

    [v-cloak] {
        display: none;
    }

    @include range;
    background: #F9F9F9;
}

.b-mortgage__btn {
    @include btn;
    @include btn-yellow;
    padding: rem(19) rem(85);
}

.b-mortgage__content {
    margin: 0 auto;
    max-width: 1100px;
}
.b-mortgage__row {
    @include media-breakpoint-up(xl) {
        @include gutter(40);
    }
}
.b-mortgage__title {
    text-align: center;
    margin-bottom: g(12);
    color: #1F1F1F;
    font-family: $Montserrat;
    font-weight: 500;
    font-size: rem(24px);
    display: none;
}
.b-mortgage__item {
    border: 1px solid #C4AB54;
    box-sizing: border-box;
    border-radius: 3px;
    background: #F1F1F1;
    padding: g(8);
    margin-bottom: g(4);
    word-wrap: break-word;
    &:last-child {
        margin-bottom: g(6);
    }
}

.b-mortgage__info-item {
    color: #1E1E1E;
    font-weight: 500;
    font-size: rem(20px);
    text-align: left;
}
.b-mortgage__no-result {
    padding: g(8) 0 g(9);
    color: #5E5E5E;
}

.b-mortgage__head {
    background: #FDFDFD;
    border: 1px solid #D7D7D7;
    box-sizing: border-box;
    border-radius: 3px;
    padding: g(6) g(8);
    margin-bottom: g(4);

}

.b-mortgage__head-item {
    color: #5E5E5E;
    font-family: $Roboto;
    font-weight: normal;
    font-size: 16px;
    text-align: left;
}

.b-mortgage__filter-label {
    color: #1A1A1A;
    font-weight: normal;
    font-size: 15px;
    margin-bottom: g(5);
}

.b-mortgage__filter-num {
    margin-bottom: g(6);
}


.b-mortgage__checkbox {
    color: #323232;
    font-family: $Karla;
    font-weight: normal;
    font-size: 16px;
    margin-bottom: g(6);

    label {

        & > span {
            display: inline-flex;
            align-items: center;
            user-select: none;

            &:before {
                content: '';
                display: inline-block;
                width: rem(25);
                height: rem(25);
                flex-shrink: 0;
                flex-grow: 0;
                border: 1px solid #E0E0E0;

                margin-right: rem(10);
                background-repeat: no-repeat;
                background-position: center center;
                background-size: 50% 50%;
                background-color: #fff;
                transition: 0.2s;
                border-radius: 50%;
            }
        }
    }

    input {
        margin-right: g(5);
        position: absolute;
        z-index: -1;
        opacity: 0;

    }

    //input:not(:disabled):active + span::before {
    //    background-color: #b3d7ff;
    //    border-color: #b3d7ff;
    //}


    input:checked + span::before {
        background-image: url("../../static/img/general/radio.svg");
    }
}


.b-mortgage__items-row {
    //margin: 0;

}

.b-mortgage__item-col {
    text-align: start;
}


.b-mortgage__filter-item {
    margin-bottom: g(10);
}

.b-mortgage__filter-percent {
    text-align: right;
}

@include media-breakpoint-down(md) {

    .b-mortgage__item {
        padding: rem(15) 0 rem(15) 0;
    }
    .b-mortgage {
        padding: g(12) 0 g(12);
    }
    .b-mortgage__filter-item {
        margin-bottom: g(11);
    }
    .b-mortgage__checkbox {
        color: #323232;
        font-family: $Karla;
        font-weight: normal;
        font-size: 16px;
        margin-bottom: g(6);

        label>span:before {
            width: rem(35);
            height: rem(35);
        }
    }
    .b-mortgage__filter-label {
        font-size: 18px;
    }
}


@include media-breakpoint-down(sm) {

    .b-mortgage__item {
        padding: rem(15) 0 rem(15) 0;
    }
    .b-mortgage__items-row {
        text-align: end;
    }
    .b-mortgage__head {
        text-align: center;
        padding: rem(13) 0;
    }
    .b-mortgage__head-item {
        font-size: rem(20px);
    }
    .b-mortgage__filter-item {
        margin-bottom: g(12);
    }
    .b-mortgage__title {
        display: block;
    }
}

@include media-breakpoint-down(xs) {
    .b-mortgage__head-item {
        font-size: 12px;
    }
    .b-mortgage__info-item {
        font-size: 12px;
    }
    .b-mortgage__items-row {
        @include gutter(3);
    }
    .b-mortgage__head {
        padding-left: 5px;
        padding-right: 5px;
    }
    .b-mortgage__item {
        padding-left: 5px;
        padding-right: 5px;
    }
    .b-mortgage__filter-item {
        margin-bottom: g(12);
    }
}
