.b-nav__name {
    font-family: $Montserrat;
    font-weight: 500;
    color: #1F1F1F;
    background: rgba(255, 255, 255, 0.9);
    margin-bottom: g(9);
    padding: g(8) g(9);
    display: inline-block;
    border-radius: 3px;

}
.b-nav__items {
    display: flex;
    flex-wrap: wrap;
}
.b-nav__item {
    line-height: 1;
    display: flex;
    align-items: center;
    padding: 2px 0;
    &:not(:first-child) {
        &:before {
            content: '';
            display: inline-block;
            width: 7px;
            height: 13px;
            background: url(../img/general/nav.svg);
            background-size: 100% 100%;
            margin: 0 g(5);
        }
    }
}

.b-nav__link {
    font-family: $SFProDisplay;
    font-size: 15px;
    font-weight: normal;
    color: #FFF;
    line-height: 1;

}
a.b-nav__link {
    @include link-h;
}
.b-nav__end {
    font-family: $SFProDisplay;
    font-size: rem(15px);
    font-weight: normal;
    color: #FFF;
}
@include media-breakpoint-down(md) {
    .b-nav__end {
        font-size: rem(20);
    }
    .b-nav__start {
        font-size: rem(20);
    }
}
@include media-breakpoint-down(xs) {
    .b-nav__link {
        font-size: 12px;
    }
}
