.b-photo-about {
    @include media-breakpoint-up(md) {
        text-align: right;
    }
}

.b-photo-about__desc {
    background: #114034;
    box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.08);
    border-radius: 3px;
    max-width: rem(570px);
    padding: g(9);
    display: inline-block;
    margin: g(11);
}
.b-photo-about__title {
    color: #FFFFFF;
    font-family: $Montserrat;
    font-weight: 500;
    font-size: 20px;
    margin-bottom: g(5);
    b,strong {
        color: #C4AB54;
        font-weight: 400;
    }
}
.b-photo-about__text {
    color: #FFFFFF;
    line-height: 1.6;
    &>* {
        &:last-child {
            margin-bottom: 0;
        }
    }
    ul {
        text-align: start;
        padding-left: 1em;
    }
}

.b-photo-about__img {
    background-size: cover;
    background-position: center;
    flex-grow: 1;
    min-height: #{'min(590px, 55vh)'};
    margin-bottom: g(11);
    border: 1px solid #D7D7D7;
    box-sizing: border-box;
    border-radius: 3px;
}

.b-photo-about__left {
    text-align: left;
    margin-left: rem(30);

    .b-photo-about__desc {
        margin-top: g(11);
        margin-left: rem(40);
    }
}
@include media-breakpoint-down(xs) {
    .b-photo-about__title {
        font-size: 18px;
    }
    .b-photo-about__text {
        font-size: 14px;
    }
}
