.b-rassrochka-big-cards {
    padding: rem(50) 0;
    background: #F9F9F9;
    overflow: hidden;
}
.b-rassrochka-big-cards__text {
    text-align: center;
    @include margin-bottom-not-last(g(12));

}
.b-rassrochka-big-cards__title {
    font-family: $Montserrat;
    font-style: normal;
    font-weight: 700;
    font-size: rem(24);
    line-height: 1.2;
    color: #000;
    @include margin-bottom-not-last(5px);
}
.b-rassrochka-big-cards__description {
    font-family:$Montserrat;
    font-style: normal;
    font-weight: 500;
    font-size: rem(18);
    line-height: 1.2;

    color: #A7A7A7;
}
.b-rassrochka-big-cards__row {
    @include gutter(50);
}

@include media-breakpoint-down(md){
    .b-rassrochka-big-cards__text{
        @include margin-bottom-not-last(g(11));
    }
    .b-rassrochka-big-cards__row{
        @include gutter-y(30);
    }
}
@include media-breakpoint-down(xs){
    .b-rassrochka-big-cards__row{
        @include gutter-y(15);
    }
}

