
.b-add {

}
.b-add__row {
    align-items: center;
    @include media-breakpoint-up(lg) {
        @include gutter(20);
    }
}
.b-add__img {
    position: relative;
    img {
        width: 100%;
        border-radius: 3px;
    }
}
.b-add__img-text {
    //max-width: 200px;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    padding: g(10) g(11);
    color: #FFFFFF;
    font-family: $Montserrat;
    font-weight: 500;
    font-size: rem(28px);
}
.b-add__yell-text {
    color: #C4AB54;
}
@include media-breakpoint-down(ld) {
    .b-add {
        ////padding: g(12) 0;
    }
}
@include media-breakpoint-only(md) {
    .b-add__img-text {
        font-size: 18px;
    }
}
@include media-breakpoint-down(md) {
    .b-add__img-text {
        padding: 20px;
    }
    .b-add__img {
        margin-bottom: g(10);
    }

}
@include media-breakpoint-down(sm) {
    .b-add {
        //padding: g(10) 0;
    }
}
