.b-rassrochka-order {

}
.b-rassrochka-order__container {
    overflow: hidden;
}
.b-rassrochka-order__title {
    color: #1F1F1F;
    font-family: $Montserrat;
    font-weight: 500;
    font-size: rem(24px);
    text-align: center;
    @include margin-bottom-not-last(g(10));
    background: #fff;
    text-align: center;
}

.b-rassrochka-order__block {

}
.b-rassrochka-order__img-cont {
    @include margin-bottom-not-last(rem(40));
    position: relative;
    display: inline-block;
    &:before {
        content: '';
        position: absolute;
        top: 50%;
        right: 100%;
        height: 1px;
        background: #C4AB54;
        width: 25vw;
    }
    &:after {
        content: '';
        @include abs-contered;
        width: calc(100% + 100px);
        height: 5px;
        background: #fff;
        z-index: 3;
    }
}
.b-rassrochka-order__img {
    position: relative;
    z-index: 5;

}
.b-rassrochka-order__name {
    font-family: $Montserrat;
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 1.2;
    color: #333333;
    min-height: calc(16px * 1.2 * 2);
    @include margin-bottom-not-last(rem(20));
}
.b-rassrochka-order__text {
    font-family: $Montserrat;
    font-style: normal;
    font-weight: 300;
    font-size: 16px;
    line-height: 1.2;
    color: #333333;
}
.b-rassrochka-order__more{
    display: flex;
    justify-content: center;
}
.b-rassrochka-order__more-btn{
    @include btn;
    @include btn-transparent;
}
.b-rassrochka-order__row{
    @include margin-bottom-not-last(g(10));
}
@include media-breakpoint-down(lg){
    .b-rassrochka-order__name{
        font-size: 14px;
        min-height: calc(14px * 1.2 * 2);
    }
    .b-rassrochka-order__text{
        font-size: 14px;
    }
}

@include media-breakpoint-down(md){
    .b-rassrochka-order__row {
        @include gutter-y(rem(40));
    }

    .b-rassrochka-order__img-cont{
        &:before{
            display: none;
        }
        &:after{
            display: none;
        }
        @include margin-bottom-not-last(rem(20))
    }
    .b-rassrochka-order__name{
        min-height: calc(14px * 1.2 * 1);
    }
}

@include media-breakpoint-down(xs){
    .b-rassrochka-order__name{
        font-size: 13px;
        min-height: calc(13px * 1.2 * 3);
    }
    .b-rassrochka-order__text{
        font-size: 13px;
    }
    .b-rassrochka-order__img{
        width: 40px;
    }
}

