.b-mort-calcul-item {
    border: 1px solid #C0AB61;
    background: #FFFFFF;
    padding-top: g(9);
    padding-left: g(9);
    flex-grow: 1;
    height: 100%;
}

.b-mort-calcul-item__content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
}

.b-mort-calcul-item__title {
    font-family: $Montserrat;
    font-weight: 500;
    font-size: rem(28px);
    color: #203E34;
    margin-bottom: g(10);
}

.b-mort-calcul-item__text {
    font-family: $SFProDisplay;
    font-weight: normal;
    font-size: rem(22px);
    letter-spacing: -0.01em;
    color: #203E34;
}

.b-mort-calcul-item__img {
    text-align: end;
    padding-right: g(4);
}

.b-mort-calcul-item__desc {
    padding-right: g(9);
}
