.b-repairs-item {
    background: #FFFFFF;
    border: 1px solid #C4AB54;
    box-sizing: border-box;
    box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.08);
    border-radius: 3px;
    height: 100%;
}

.b-repairs-item__title {
    color: #1E1E1E;
    font-family: Montserrat;
    font-weight: 500;
    font-size: rem(20px);
    margin-bottom: g(5);
}

.b-repairs-item__text {
    color: #343434;
    font-weight: normal;
    font-size: 15px;
    line-height: 1.6;
}
.b-repairs-item__img {
    img {
        width: 100%;
    }
}
.b-repairs-item__desc {
    padding: g(10);
}
@include media-breakpoint-down(xs) {
    .b-repairs-item__title {
        font-size: 16px;
    }
    .b-repairs-item__text {
        font-size: 14px;
    }
}

