.b-options-desc {
    padding-top: g(12);
    padding-bottom: g(12);
    margin: 0 auto;
    max-width: rem(850);
}
.b-options-desc__title {
    color: #1F1F1F;
    font-family: $Montserrat;
    font-weight: 500;
    font-size: rem(24px);
    margin-bottom: g(9);
}
.b-options-desc__text {
    color: #343434;
    font-size: 15px;
    max-width: rem(450);
}
@include media-breakpoint-down(md) {
    .b-options-desc {
        max-width: 100%;
    }
    .b-options-desc__text {
        max-width: 100%;
    }
}
