.b-instal-advantages__content {
    margin: 0 auto;
    max-width: 1000px;
}
.b-instal-advantages__content {
    max-width: 1000px;
    margin: 0 auto;
}
.b-instal-advantages__row {
    margin-bottom: -30px;
}

.b-instal-advantages__col {
    margin-bottom: 30px;
}

.b-instal-advantages__title {
    text-align: center;
    color: #1F1F1F;
    font-family: $Montserrat;
    font-weight: 500;
    font-size: rem(24px);
    margin-bottom: g(11);
}

.b-instal-advantages__item {
    text-align: start;
    padding: rem(20) rem(20) rem(45) rem(25);
    border: 1px solid #B9B9B9;
    box-sizing: border-box;
    box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.08);
    border-radius: 3px;
    height: 100%;
}

.b-instal-advantages__item-title {
    color: #C4AB54;
    font-family: $Montserrat;
    font-weight: 500;
    font-size: rem(18px);
    line-height: 1.3;
    margin-bottom: g(5);
}

.b-instal-advantages__item-text {
    color: #343434;
    font-weight: normal;
    font-size: 15px;
}


@include media-breakpoint-down(md) {
    .b-instal-advantages__item {
        margin-bottom: g(8);
    }

}

@include media-breakpoint-down(sm) {
    .b-instal-advantages__col {
        margin-bottom: g(9);
    }
}

