.b-buy {
    background: #fff;
    border-radius: 3px;
    border: 1px solid #D7D7D7;
    display: block;
    color: #000;
    height: 100%;
    position: relative;
}
.b-buy__checkbox {
    position: absolute;
    top: 0;
    left: 0;
}
.b-buy__content {
    height: 100%;
    display: flex;
    flex-direction: column;
}
.b-buy__img {
    border-radius: 3px;
    border-bottom: 1px solid #D7D7D7;
    padding: g(7);
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    img {
        height: 220px;
        object-fit: contain;
    }
}

.b-buy__type {
    font-family: $SFProDisplay;
    font-size: 12px;
    line-height: 1;
    background: #C4AB54;
    color: #fff;
    border-radius: 3px;
    padding: g(4) g(6);
    display: block;
    display: inline-block;
    margin-bottom: g(6);
    text-align: left;
    align-self: flex-start;
}

.b-buy__rooms {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: g(6);
    margin-top: auto;
}

.b-buy__rooms-text {
    font-family: $SFProDisplay;
    color: #1E1E1E;
    font-size: rem(21px);
}
.b-buy__price-cont {
    margin-bottom: g(6);
    font-size: rem(19px);
    display: grid;
    grid-template-columns: repeat(2, max-content);
    align-items: center;
    gap: 10px;
}
.b-buy__price {
    color: #363636;
    font-family: $SFProDisplay;
    text-align: left;
    font-weight: 500;
    &_primary {
        background: #C4AB54;
        color: #fff;
        padding: g(1) g(4);
    }
}
.b-buy__sale-ico {
    position: absolute;
    top: 0;
    right: 0;
    background: #C4AB54;
    color: #fff;
    padding: g(4);
    aspect-ratio: 1/1;
    font-size: 16px;
    min-width: 30px;
    line-height: 1;
    display: flex;
    align-items: center;
    justify-content: center;
}
.b-buy__price-old {
    font-size: 0.75em;
    text-decoration: line-through;
    opacity: 0.6;
}
.b-buy__items {
    //margin-bottom: auto;
}
.b-buy__item {
    color: #1C1C1C;
    font-family: $SFProDisplay;
    font-weight: 500;
    font-size: 15px;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    padding-bottom: g(8);
}

.b-buy__item > span {
    color: #1E1E1E;
    font-weight: normal;
    margin-left: 3px;
    display: inline-block;
    white-space: nowrap;
}

.b-buy__btn {
    @include btn;
    @include btn-yellow;
    @include btn-block;
    margin-top: g(7);
}

.b-buy__bottom {
    text-align: left;
    padding: rem(20);
    flex-grow: 1;
    display: flex;
    flex-direction: column;
}
.b-buy__img-mask {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 15;
    background: rgba(#134034, 0.8);
    display: flex;
    align-items: center;
    justify-content: center;
    padding: g(5);
}
.b-buy__mask-text {
    font-size: rem(24px);
    color: #FFFFFF;
}
.b-buy_disabled {
    pointer-events: none;
    .b-buy__rooms {
        margin-top: 0;
        margin-bottom: auto;
    }
    .b-buy__items {
        margin-top: g(6);
    }

}
@include media-breakpoint-down(sm) {
    .b-buy__img {
        padding: 10px 5px;
        img {
            height: 160px;
        }
    }
    .b-buy__bottom {
        padding: 10px 5px;
    }
    .b-buy__price {
        font-weight: 600;
    }
    .b-buy__price-old {
        font-size: 12px;
        font-weight: 600;
    }
    .b-buy__price-cont {
        margin-top: auto;
    }
}
@include media-breakpoint-down(xs) {
    .b-buy__price-cont {
        grid-template-columns: max-content;
        gap: 5px;
    }
    .b-buy__price-old {
        order: -1;
    }
    .b-buy__btn {
        font-size: 12px;
        margin-top: g(5);
        padding: g(4);
    }
    .b-buy__item {
        font-size: 12px;
        padding-bottom: g(5);
    }
}

