

.b-promotions-slider {
    .swiper-button-prev,
    .swiper-button-next {
        position: relative;
        z-index: 10;
        cursor: pointer;
        background: none;
        align-items: center;
        display: flex;
        justify-content: center;
        background: rgba(255, 255, 255, 0.67);
        width: rem(37px);
        height: rem(37px);
        transition: 0.2s;
        background: #C4AB54;
        border-radius: 3px;
        padding: rem(10) rem(13);
        fill: #fff;
        left: auto;
        margin: 0;
    }

    .swiper-button-next {
        right: 0;
    }

    .swiper-button-prev {
        transform: rotate(180deg);
        margin-right: g(7);
    }
    &_length-1 {
        .b-promotions-slider__nav {
            opacity: 0;
        }
    }
}
.b-promotions-slider__row {
    align-items: center;
}
.b-promotions-slider__nav {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    margin-bottom: g(8);
}

.b-promotions-slider__content {
    position: relative;
}

.b-promotions-slider__title {
    font-weight: 700;
    font-size: rem(20px);
    color: #343434;
    margin-bottom: g(10);
}

.b-promotions-slider__text {
    font-weight: 400;
    font-size: 15px;
    color: #343434;
    margin-bottom: g(10);
}

.b-promotions_reverse {
    .b-promotions_row {
        flex-direction: row-reverse !important;
    }
}

