.b-person {
    text-align: center;
}
.b-person__tel {
    text-align: center;
}
.b-person__img-cont {
    max-width: 144px;
    margin: 0 auto;
    margin-bottom: g(6);
}
.b-person__img {
    padding-bottom: 100%;
    background-size: cover;
    background-position: top;
    border-radius: 50%;
    box-shadow: inset 0 0 0 4px rgba(196, 171, 84, 0.57);
}
.b-person__name {
    color: #1E1E1E;
    font-weight: 500;
    font-size: rem(20px);
    margin-bottom: g(6);
}
.b-person__employee {
    color: #1C1C1C;
    margin-bottom: g(6);
}
.b-person__tel {
    color: #1C1C1C;
    font-size: rem(17px);
    @include link-h;
}
@include media-breakpoint-down(md) {
    .b-person__tel {
        font-size: rem(22px);
    }
}
@include media-breakpoint-down(xs) {
    .b-person__name {
        font-size: 16px;
    }
    .b-person__employee {
        font-size: 12px;
    }
}
