.b-download-doc {
    padding: g(12) 0;
}
.b-download-doc__blocks {

}
.b-download-doc__block {
    max-width: rem(870px);
    margin: 0 auto;
    &:not(:last-child) {
        margin-bottom: g(11);
    }
}
.b-download-doc__item {
    display: flex;
    //align-items: center;
    //justify-content: space-between;
    border: 1px solid #D7D7D7;
    box-sizing: border-box;
    border-radius: 3px;
    background: #F1F1F1;
    padding: g(7) g(11) g(7) g(7);
    &:hover {
        background: #dfdfdf;
    }

}
.b-download-doc__title {
    background: #FDFDFD;
    border: 1px solid #D7D7D7;
    box-sizing: border-box;
    border-radius: 3px;
    color: #1F1F1F;
    padding: g(7) 0 g(7) g(7);
}
.b-download-doc__text {
    font-weight: 500;
    font-size: 14px;
    color: #1E1E1E;
    margin-right: g(4);
}
.b-download-doc__link {
    font-weight: 500;
    font-size: 14px;
    color: #C4AB54;
    text-align: end;
    white-space: nowrap;
    flex-shrink: 0;
    margin-left: auto;

}
@include media-breakpoint-down(md) {
    .b-download-doc__title {
        font-size: rem(22);
    }
    .b-download-doc__text {
        font-size: rem(20);
    }
    .b-download-doc__link {
        font-size: rem(20);
    }
}
@include media-breakpoint-down(sm) {
    .b-download-doc__item {
        flex-direction: column;
    }
    .b-download-doc__text {
        margin-left: 0;
        margin-bottom: g(4);
    }
    .b-download-doc__link {
        text-align: left;
        margin-left: 0;
    }
}
