.b-rassrochka-cards {

}
.b-rassrochka-cards__block {
    padding: 25px;
    border: 1px solid #C4AB54;
    border-radius: 5px;
    min-height: 100%;
}
.b-rassrochka-cards__img-block {

}
.b-rassrochka-cards__row{
    align-items: center;

}
.b-rassrochka-cards__img {
    aspect-ratio: 1;
    border-radius: 5px;
}
.b-rassrochka-cards__text {

}
.b-rassrochka-cards__title {
    font-family: $Montserrat;
    font-style: normal;
    font-weight: 700;
    font-size: rem(24);
    line-height: 1.2;
    color: #000;
    @include margin-bottom-not-last(g(9))
}
.b-rassrochka-cards__description {
    font-family: $Montserrat;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 1.2;
    color: #000;
}

@include media-breakpoint-down(lg){
    .b-rassrochka-cards__title{
        font-size: 19px;
    }
    .b-rassrochka-cards__description{
        font-size: 12px;
    }
}

@include media-breakpoint-down(md){
    .b-rassrochka-cards__img-block{
        text-align: center;
    }
    .b-rassrochka-cards__row{
        @include gutter-y(30);
    }
}

@include media-breakpoint-down(xs){
    .b-rassrochka-cards__title{
        font-size: 14px;
        @include margin-bottom-not-last(g(8))
    }
    .b-rassrochka-cards__block{
        padding: 10px;
    }
    .b-rassrochka-cards__row-main{
        @include gutter-y(15);
    }
    .b-rassrochka-cards__row{
        align-items: start;
        @include gutter(5px);
        @include gutter-y(0);
    }
}


