// Mixins.

 $baseFontSize : 16;
@function rem($pixels, $context: $baseFontSize) {
    @if (unitless($pixels)) {
        $pixels: $pixels * 1px;
    }

    @if (unitless($context)) {
        $context: $context * 1px;
    }

    @return $pixels / $context * 1rem;
}
@function g($n) {
    @return var(--g#{$n});
}
@mixin min-height-in-vh($n) {
    min-height: #{$n}vh;
    min-height: calc(var(--vh, 1vh) * #{$n});
}

@mixin btn {
    display: inline-block;
    line-height: 1;
    text-align: center;
    transition: 0.2s background, 0.2s color, 0.2s border;
    position: relative;
    cursor: pointer;

    font-family: $SFProDisplay;
    color: #383431;
    font-size: 16px;
    letter-spacing: 1px;
    font-weight: 700;


    padding:  g(6) g(10);
    border: rem(3) solid #C4AB54;
    background: transparent;
    border-radius: 3px;

    &:hover {
        background: #C4AB54;
        color: #fff;
    }
    @include media-breakpoint-down(sm) {
        width: 100%;
        padding-left: 5px;
        padding-right: 5px;
    }
}
@mixin  btn-yellow {
    color: #fff;
    font-weight: 500;
    font-size: 15px;
    padding: g(7) g(10);
    background: #C4AB54;
    border-color: #C4AB54;
    box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.08);
    &:hover {
        background: #58524E !important;
        border-color: #58524E !important;
    }
}
@mixin  btn-green {
    color: #fff;
    font-weight: 500;
    font-size: 15px;
    padding: g(7) g(10);
    border-color: #174034;
    background: #174034;
    box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.08);
    &:hover {
        background: #58524E !important;
        border-color: #58524E !important;
    }
}
@mixin btn-white {
    position: relative;
    cursor: pointer;
    display: inline-block;
    line-height: 1;
    text-align: center;
    font-family: $Montserrat;
    color: #1E1E1E;
    font-weight: 500;
    font-size: rem(20px);
    padding: g(7) g(10);
    background: rgba(255, 255, 255, 0.9);
    border-radius: 3px;
    &:hover {
        background: #C4AB54;
        color: #fff;
    }
}
@mixin btn-transparent {
    color: #232323;
    font-size: 15px;
    font-weight: 500;
    border: 2px solid #000000;
    background: transparent;
    padding: g(8) g(10);
    &:hover {
        color: #fff;
        background: #58524E;
        border-color: #58524E;
    }
}
@mixin btn-block {
    display: block;
    width: 100%;
    padding-left: 5px;
    padding-right: 5px;
}
