@mixin listBold {
    li {
        font-family: $Montserrat;
        font-style: normal;
        font-weight: 600;
        font-size: 16px;
        text-transform: uppercase;
        color: #151515;
        margin-bottom: rem(20);
    }
}
@mixin list2col {
    ul {
        column-count: 2;
        column-gap: 30px;
    }
}
@mixin title {
    color: #58534E;
    font-size: rem(106);
    font-family: $SFProDisplay;
    font-weight: 300;
    line-height: 1.2;
    position: relative;
    @include media-breakpoint-down(sm) {
        font-size: rem(75);
    }
    @include media-breakpoint-down(xs) {
        font-size: rem(55);
    }

}
@mixin zoom {
    @include media-breakpoint-up(lg) {
        $galleryTransition: 0.6s;
        position: relative;
        &:before {
            content: '';
            display: block;
            position: absolute;
            top: 0;
            right: 0;
            left: 0;
            bottom: 0;
            background: rgba(45, 33, 23, 0.3);
            z-index: 5;
            opacity: 0;
            transition: $galleryTransition;
        }
        &:after {
            content: '';
            display: block;
            position: absolute;
            top: 0;
            right: 0;
            left: 0;
            bottom: 0;
            background: url(../img/general/zoom.svg);
            background-position: center center;
            background-repeat: no-repeat;
            background-size: rem(46) rem(47);
            z-index: 10;
            display: none;
            animation: top 0.6s forwards;

        }
        &:hover {
            &:before,
            &:after {
                opacity: 1;
                display: block;
            }
        }
    }
}
@mixin link-h {
    &:hover {
        color: #C4AB54;
    }
}
@mixin cover {
    background-size: cover;
    background-position: center center;
}
@mixin modal-title {
    font-weight: 300;
    font-size: rem(36);
    color: #E5E5E5;
    text-transform: uppercase;
    margin-bottom: rem(30);
}
@mixin list {
    margin: 0;
    padding: 0;
    ul {
        margin: 0;
        padding: 0;
    }
    li {
        font-weight: 400;
        display: inline-flex;
        width: 100%;
        margin-bottom: 15px;
        position: relative;
        padding-left: rem(22);
        &:before {
            content: '';
            display: inline-block;
            width: rem(8);
            height: rem(8);
            background: #C2A84D;
            margin-right: rem(13);
            flex-shrink: 0;
            position: absolute;
            top: 0.3em;
            left: 0;
        }
    }
}
@mixin testFix() {
    @media (min-height: 1400px) {
        min-height: 800px;
        height: auto;
    }
}
@mixin mainSection() {


    display: flex;
    flex-direction: column;
    @supports (display: grid) {
        min-height: 100vh;
        min-height: -webkit-fill-available;
        min-height: -moz-available;
        min-height: fill-available;
    }
    @include media-breakpoint-up(lg) {
        padding: rem(75);
    }
    @include media-breakpoint-down(xs) {
        @media (min-height: 600px) {
            min-height: auto;
        }
    }
}
