@mixin gradient-bg {
    background: linear-gradient(to bottom, #fff, #FDF9F6);
}
@mixin gutter($n) {
    @if (unitless($n)) {
        $n: $n * 1px;
    }
    margin: 0 #{$n * -1};
    & > div {
        padding: 0 #{$n};
    }
}
@mixin text-last-mb-fix() {
    &>*:last-child {
        margin-bottom: 0;
    }
}
@mixin gutter-y($n: 30px) {
    @if (unitless($n)) {
        $n: $n * 1px;
    }
    margin-bottom: #{$n * -1};
    &>div {
        margin-bottom: $n;
    }
}
@mixin margin-bottom-not-last($n) {
    &:not(:last-child) {
        margin-bottom: $n;
    }
}
@mixin abs-contered() {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}
