.b-mort-advantages {


}

.b-mort-advantages__content {

}
.b-mort-advantages__row {
    margin-bottom: -30px;
    justify-content: center;
}
.b-mort-advantages__col {
    margin-bottom: 30px;
}
.b-mort-advantages__title {
    text-align: center;
    color: #1F1F1F;
    font-family: $Montserrat;
    font-weight: 500;
    font-size: rem(24px);
    margin-bottom: g(11);
}
.b-mort-advantages__item {
    text-align: start;
    padding: rem(20) rem(20) rem(45) rem(25);
    border: 1px solid #B9B9B9;
    box-sizing: border-box;
    box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.08);
    border-radius: 3px;
    height: 100%;
}
.b-mort-advantages__item-title {
    color: #C4AB54;
    font-family: $Montserrat;
    font-weight: 500;
    font-size: rem(18px);
    line-height: 1.3;
    margin-bottom: g(5);
}
.b-mort-advantages__item-text {
    color: #343434;
    font-weight: normal;
    font-size: 15px;
}
.b-mort-advantages_num {
    .b-advantages__item-title {
        span {
            font-size: rem(25px);
            font-weight: 600;
            margin: 0 g(2);
        }
    }
}

@include media-breakpoint-down(lg) {
    .b-mort-advantages__row {
        //width: 100%;
    }
}
@include media-breakpoint-down(md) {
    .b-mort-advantages__item {
        margin-bottom: g(8);
    }

}
@include media-breakpoint-down(sm) {
    .b-mort-advantages__col {
        margin-bottom: g(9);
    }
}
