.b-range {
    .irs--flat .irs-from, .irs--flat .irs-to, .irs--flat .irs-single {
        border-radius: 3px;
        padding: rem(3);
        font-size: rem(15);
        font-family: $SFProDisplay;
        cursor: pointer;
    }

    .irs--flat .irs-handle {
        background: #C4AB54;
    }

    .irs--flat .irs-line {
        top: 25px;
        height: 12px;
        background-color: #AEAEAE;
        border: 4px solid #AEAEAE;
    }

    .irs--flat .irs-bar {
        background: #C4AB54;
    }

    .irs--flat .irs-from:before, .irs--flat .irs-to:before, .irs--flat .irs-single:before {
        display: none;
    }

    .irs--flat .irs-handle > i:first-child {
        display: none;
    }

    .irs--flat .irs-min, .irs--flat .irs-max {
        color: #1A1A1A;
        font-family: $SFProDisplay;
        font-size: rem(15px);
        background: none;
        top: rem(34px);
    }

    .irs--flat .irs-handle {
        cursor: pointer;
    }

    .irs-line {
        cursor: pointer;
    }

    .irs-from, .irs-to, .irs-single {
        //top: rem(19px);
    }

    .irs--flat .irs-min, .irs--flat .irs-max {
        display: none;
    }

    .irs-from {
        top: rem(-5px);
        color: #000;
        background: none;
    }

    .irs-to {
        top: rem(-5px);
        color: #000;
        background: none;
    }

    .irs-handle {
        width: rem(21px);
        height: rem(21px);
        box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.08);
        border-radius: 3px;
    }

    .irs-single {
        background: #C4AB54;
        top: rem(-6);
    }

    .irs {
        max-width: 100%;
    }

}

@include media-breakpoint-down(sm) {
    .b-range {
        .b-range .irs {
            max-width: 100%;
        }
        .irs--flat .irs-handle {
            top: 24px;
        }
        .irs--flat .irs-line,
        .irs--flat .irs-bar {
            height: 5px;
            border: none;
        }
        .irs--flat .irs-handle {
            top: 21px;
        }
    }

}

